<template>
	<div>
		<!-- 广告轮播图 -->
		<ad></ad>
		<!-- 模块列表 -->
		<div class="content" v-if="top">
			<div @click="btnRight" class="cont_fx fx_l"><img src="../../gzxtkt/images/xs_pic_more02.png" class="fx_img"/></div>
			<div @click="btnLeft" class="cont_fx fx_r"><img src="../../gzxtkt/images/xs_pic_left.png"  class="fx_img" /></div>	
				<div class="panel">	
				<div class="msdx"> 
				<router-link to="/msdx">
					<img src="../../gzxtkt/images/msdx.png" class="icon_msdx"></img>
					<p class="ic_msdx">名师导学</p>	
				</router-link>	
				</div>		
				<div v-for="(r,v) in columnList" id="act" class="panel_li" @click="modCont(v)" >
					<img :src="imgList[v]" class="icon_0"></img>
					<span v-show="r.flag" class="icon_1"></span>
					<p>{{r.name}}</p>
				</div>	
			</div>
			
		</div>

		
		<!-- 悬浮模块列表 -->
		<div class="content_flex" v-if="!top">
			<div @click="btnRight" class="cont_fx fx_l"><img src="../../gzxtkt/images/xs_pic_more02.png" class="fx_img"/></div>
			<div @click="btnLeft" class="cont_fx fx_r"><img src="../../gzxtkt/images/xs_pic_left.png"  class="fx_img" /></div>
				<div class="panel">
				<div class="msdx"> 
				<router-link to="/msdx">
					<img src="../../gzxtkt/images/msdx.png" class="icon_msdx"></img>
					<p class="ic_msdx">名师导学</p>	
				</router-link>	
				</div>			
				<div v-for="(r,v) in columnList" id="act" class="panel_li" @click="modCont(v)" >
					<img :src="imgList[v]" class="icon_0"></img>
					<span v-show="r.flag" class="icon_1"></span>
					<p>{{r.name}}</p>
				</div>	
			</div>
			
		</div>	

      <mt-loadmore :bottomDistance=70 :bottomDropText="null" :bottomPullText="null" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" >
		<!-- 资讯列表 -->
		<div style=" margin-bottom: 0; ">
			<div class="jyzx">
				<span class="jyzx_title"><span class="title_img" >&nbsp;</span>{{Name}}</span>
			</div>
			<router-link :to="'moduleNews?demo='+obj">
            
				<div class="jyzx_list" v-for="(item,v) in changList" :key='item.v' @click="listID(v)">
					<div class="jyzx_r">
						<div class="jyzx_titil" >{{item.title}}</div>
						<div class="record">
							<span style="color: #0099CC; padding: 0.04rem 0.08rem; border-radius: 0.05rem; ">{{item.channel_name}}</span>
						</div>
					</div>
					<img :src="imgurl+item.title_img" class="jyzx_l">
			    </div>
            
			</router-link>
		</div>
		</mt-loadmore>
		<foot :show="1"></foot>
	</div>
</template>

<script>
	import f from './footer.vue'
	import ad from './ad.vue'
	import kwz from '../utils/kwz.js'
	import { Loadmore } from 'mint-ui'
	export default {
		data(){
			return {
				imgurl:'',
				currId:'',
				columnList:[],
				changList:[],
				modelID:null,
				some:null,
				Name:null,
				arr:[],
				obj:[],
				ID:[],
				page:1,
				top:true,
				scroll: '',
				ountall: 0,
				pagesize: 10,
				allLoaded:false,
				// moID:11,
				imgList:["../../gzxtkt/images/jy.png","../../gzxtkt/images/zj.png","../../gzxtkt/images/zjzl.png","../../gzxtkt/images/jzlf.png","../../gzxtkt/images/jzgy.png","../../gzxtkt/images/1.png","../../gzxtkt/images/nodatas.png","../../gzxtkt/images/4.png","../../gzxtkt/images/mail_folder.png","../../gzxtkt/images/2.png","../../gzxtkt/images/app_wzslh.png","../../gzxtkt/images/zsjy.png","../../gzxtkt/images/pic_hot.png","../../gzxtkt/images/school.jpg",]
			}
		},
		created: function(){
			this.imgurl= (kwz.project+"/").replace("api/wx/","");
			if(!kwz.getColumnAll||!kwz.getInfoByColumn){
				// $("#act p").eq(1).addClass('active');
				var that=this;
				//请求模块
				var url = kwz.project + '/getColumnAll'
				var id_cal=null;
				kwz.acall2({
					url : url,
					params : null,
					type : 'GET',
					success: function(data){
						if(data.StatusCode==200){
							that.columnList=data.data;
							that.Name = data.data[0].name;
							that.currId=that.columnList[0].id;
							kwz.getColumnAll=data.data;
							that.seccond();
						}else{
							alert("网络异常，稍后重试！");
						}
					}
				})
			}else{
				this.columnList=kwz.getColumnAll;
				this.Name=kwz.getColumnAll[0].name;
				this.currId=kwz.getColumnAll[0].id;
				this.changList = kwz.getInfoByColumn.T1;
				this.countall=kwz.getInfoByColumn.T0[0].countall;
			}
		},
		components: {
			'foot': f, // 添加底部导航组件
			'ad':ad
		},
		mounted() {
			window.addEventListener('scroll', this.menu)
		},
		methods: {

			//监听滚动事件
			menu() {
				this.scroll = document.body.scrollTop;
				if (this.scroll >= 240) {
					this.top = false;
				}else{
					this.top = true;
				}
			},
			modCont (v) {
				$("#act p").eq(0).addClass('s')
				$("#act p").eq(v).addClass('active').parent().siblings().find('p').removeClass('active');
				// this.moID = this.columnList[v].id;
				this.some = this.columnList[v].id;
				let newsID = this.some;
				this.Name =  this.columnList[v].name;
				var that=this;
				var url_2 = kwz.project + '/getInfoByColumn?id='+newsID+'';
				kwz.acall2({
					url : url_2,
					params : null,
					type : 'GET',
					success: function(data){
						if(data.StatusCode==200){
							that.changList = data.data.T1
						}else{
							alert("网络异常，稍后重试！");
						}
					}
				})	
			},
			//获取每个列表的ID
			listID (v) {
				var that=this;
				let neID1 = that.changList[v].content_id;
				let neID2 = that.currId;
				that.obj.push(neID1);
				that.obj.push(neID2);
			},
			seccond(){
			var that=this;
			var url_1 = kwz.project +'/getInfoByColumn?id='+this.currId+'';
			kwz.acall2({
				url : url_1,
				params : null,
				type : 'GET',
				success: function(data){
					if(data.StatusCode==200){
						that.changList = data.data.T1;
						that.countall=data.data.T0[0].countall;
						kwz.getInfoByColumn=data.data;
					}else{
						alert("网络异常，稍后重试！");
					}
				}
			})

			},
		   loadBottom() {//上拉分页
		   	if(this.page>this.countall/this.pagesize){
				 this.allLoaded = true;// 若数据已全部获取完毕
  				 this.$refs.loadmore.onBottomLoaded();
			}
		   	this.page++;
		   	var that=this;
		   	setTimeout(() => {
			var url_1 = kwz.project +'/getInfoByColumn?id='+this.currId+'&page='+this.page;
			kwz.acall2({
				url : url_1,
				params : null,
				type : 'GET',
				success: function(data){
					if(data.StatusCode==200){
						that.changList=that.changList.concat(data.data.T1);
					}else{
						alert("网络异常，稍后重试！");
					}
				}
			})
				 }, 2000);
            // this.$refs.loadmore.onTopLoaded();
           },
			//点击向左移动
			btnRight() {
				if ($('.panel').offset().left<=-300) {
					// $('.panel').animate({left: '0'}, 1000);
				}else{
					$('.panel').animate({left: '-=7rem'}, 100); 
				}
			},
			//点击向右移动
			btnLeft () {
				let w = $('.panel').offset().left;
				if ($('.panel').offset().left >= 17 && $('.panel').offset().left <= 60) {
					$('.panel').animate({left: '0'}, 100);
				}else{
					$('.panel').animate({left: '+=7rem'}, 100);
				}
			}

		}		
	}
</script>

<style scoped>
	.title_img{
		background-color: #70a1e8; 
		margin-right: 0.1rem;}
	.jyzx{
		width: 100%; 
		/*height: 0.88rem; */
		background-color: #fff;
		/*margin-top: 0.2rem;*/
		border-bottom: 0.01rem solid #e7e7e7;
		padding: 0 0.2rem ; 
		font-size: 0.36rem;
		display: flex;
		align-items: center; 
		justify-content: space-between;
		margin-bottom: -.35rem;
	}
	.jyzx_title{
		color: #70a1e8; 
	    margin-bottom: .2rem; 
	}
	.yjzc_more{
		font-size: 0.28rem; 
		color: #838383;  
	}
	.yjzc_more_img{
 		width: 0.16rem; 
 		height: 0.26rem; 
 		margin-left: 0.1rem;
	}

	.jyzx_list{
		width: 100%;  
		height: 1.6rem; 
		background-color: #fff; 
		border-bottom: #e7e7e7 0.01rem solid;
		overflow: auto;
		flex-direction: row;
		align-items: center; 
		justify-content: space-between;
	}
	.jyzx_l{
		width: 1.8rem; 
		height:1.18rem; 
		border-radius: 0.03rem;  
		text-align: center; 
		vertical-align: middle;
		margin:0.2rem; 
	}
	.jyzx_r{
		height: 100%; 
		width: 4.8rem; 
		margin: 0rem 0.2rem; 
		float: left; 
	}
	.record {
		display: flex;
		align-items: center; 
		justify-content: space-between;
		width: 100%; 
		height: 0.6rem;
		font-size: 0.28rem; 
		color: #828282; 
	}
	.jyzx_titil{
		color:#333; 
		font-size:0.34rem; 
		line-height: 0.7rem; 
		margin-top: 0.2rem;
		overflow:hidden; 
		text-overflow:ellipsis; 
		white-space:nowrap;
	}
	.content{
		margin-bottom: 0.2rem;
		width: 100%;
		height: 100%; 
		background-color: #fff;
		overflow: auto;	
	}
	.content_flex{
		position:fixed; top:0; width:100%;
		background-color: white;
		z-index: 999;
		/* display: none; */
	}
	.cont_fx{
		width: 5%; 
		background-color: #fff; 
		position: absolute;  
		z-index: 999;height: 2rem;  
		vertical-align: middle;
	}  
	.fx_l{ 
		right: 0;
		text-align: left;
	}
	.fx_r{ 
		left: 0;
		text-align: right;
	}
	.fx_img{ 
		width: 0.20rem; 
		height: 0.36rem;
		margin-top: 0.85rem;
	}
	.panel{
		overflow-y:hidden;
		display: flex;
		display: -webkit-flex;
		flex-flow: row;
		width: 185%;
		height: 2rem; margin: 0 5%;
		position: relative;
		margin-top: .1rem;
	}
	.panel_li{
		position: relative;
		padding: 0.35rem 0.13rem;
		width: 1.7rem;
		display: block; 
	}
	.icon_1{
		position: absolute; 
		top: 0.25rem;
		right: 0.4rem;
		display: inline-block;
		width: 0.17rem;
		height: 0.17rem;
		background-color: #ff6666;
		border-radius: 100%;
	}
	.icon_0{
		margin: 0 auto;
		display: block;
		width: 0.88rem;
		height: 0.88rem;
	}
	.icon_msdx{
		margin: 0 auto;
		display: block;
		width: 0.88rem;
		height: 0.88rem;
		margin-top: -.71rem;
	}
	.panel_li p{
		margin-top: 0.22rem;
		font-size: 0.28rem;
		text-align: center;
		color: #838383;
	}
	.s{
		color: #838383!important;
	}
	.active{
		color: #0caedf!important;
	}

	#act:first-child p{
		color: #0caedf;
	}
	.msdx{
		 padding: .59rem .3rem;
		 font-size: .28rem;
		 margin-right: -.1rem;
	}
	.ic_msdx{
		 margin-top: .265rem;
		     /*margin-top: .18rem;*/
		 color: #838383;
	}

</style>



