<template>
	<div style="overflow: auto;">
		<header>
			<router-link to='/main' class="goback"></router-link>
			<p>缴费服务</p>
		</header>
		
       	<div class="main1" style="margin-top: 1.2rem;">
       		<div style="display:flex;">
       			<div class="jImg"><img :src="imgurl+proimg" style="width:100%;height: 1.04rem;border-radius:0.1rem"></div>
       			<div class="jMsg">
       				<a>{{msg.name}}</a>
       				
       			</div>
       		</div>						
			<p style="border-top: 0.01rem solid #e7e7e7;border-bottom: none;color:red;margin-top:0.3rem"><span class="groud"></span>RMB<span class="count jf">{{msg.price}}元</span></p>

			<div v-if="stu.length>1">
				<div class="sel">
					<div class="jImg" style="flex:0.3;"><span>学生姓名</span></div>
					<div class="jMsg" style="flex:0.7;link;margin-top:0.24rem">
						<select v-model='selected'>
							<!-- <option checked value="10">请选择</option> -->
							<option v-for="(item,v) in stu" :value='v' :key="v">{{item.stuname}}</option>

						</select>
					</div>
				</div>
				<p><span class="groud"></span>学生班级<span class="count">{{stu[selected].stuclass}}</span></p>
				<p><span class="groud"></span>缴费状态<span class="count">
					<div v-if="stu[selected].pay==1">已缴费</div>
					<div v-else>未缴费</div>
				</span></p>
				<div  v-if="stu[selected].pay==0">
					<!-- <router-link :to="'login'"> -->
						<div style="padding: 0 0.2rem;margin-top:0.15rem">
							<button class="tj" v-if="isDisable" @click="submit(stu[selected].stuid)">立即缴费</button>
							<button class="tj" v-else @click="submit(stu[selected].stuid)">立即缴费</button>
						</div>
					<!-- </router-link> -->
				</div>
				<div v-else>
					<div style="padding: 0 0.2rem;margin-top:0.15rem">
						<button class="ytj">立即缴费</button>
					</div>
				</div>

			</div>

			<div v-else>
				<p style="border-top: 0.01rem solid #e7e7e7;"><span class="groud"></span>学生姓名<span class="count">{{uname}}</span></p>
				<p><span class="groud"></span>学生班级<span class="count">{{stuclass}}</span></p>
				<p><span class="groud"></span>缴费状态<span class="count">
				<div v-if="pay==1">已缴费</div>
				<div v-else>未缴费</div>
				</span></p>
				<div v-if="pay==0">
					<!-- <router-link :to="'login'"> -->
						<div style="padding: 0 0.2rem;margin-top:0.15rem">
							<button class="tj" v-if="isDisable" @click="submit(stuid)">立即缴费
							</button>
							<button class="tj" v-else @click="submit(stuid)">立即缴费
							</button>
						</div>
					<!-- </router-link> -->
				</div>
				<div v-else>
					<div style="padding: 0 0.2rem;margin-top:0.15rem">
						<button class="ytj">立即缴费</button>
					</div>
				</div>
			</div>


		</div>
	</div>
</template>

<script>
	import kwz from '../utils/kwz.js'
	export default {
		data(){
			return {
				msg: {},
				isDisable : false,
				imgurl:'',
				stu:[],
				selected:0,
				uname:'',
				stuclass:'',
				stuid:'',
				pay:'',
				proimg:'',
				code : ''//订单号
			}
		},
		created: function(){
			this.imgurl= (kwz.project+"/").replace("api/wx/","");
			var that=this;
			var url = kwz.project + '/getJfDetails'
			debugger
			kwz.acall2({
				url : url,
				params : null,
				type : 'GET',
				success : function(data){
					if(data.StatusCode==200){
						that.stu = data.data.stulist;
						that.uname=data.data.stulist[0].stuname;
						that.stuclass=data.data.stulist[0].stuclass;
						that.stuid=data.data.stulist[0].stuid;
						that.pay=data.data.stulist[0].pay;
						that.proimg=data.data.proimg;

						// console.log(that.stu);
						// that.msg=data.data;
					}else{
						alert("网络异常，稍后重试！");
					}
				},
			 })
			var url = kwz.project.replace('api/wx','servlet/CenterHandleServlet')
			kwz.acall2({
				url : url,
				params : {
					type : 6
				},
				type : 'GET',
				success : function(ret){
					var data = ret.data
					if(ret.StatusCode == 200){
						that.msg = data;
					}else{
						alert("网络异常，稍后重试！");
					}
				}
			})
		},
		methods: {
			submit(id){
				// alert("学生ID:"+id);
				// alert("产品ID:"+this.msg.proid)
				this.isDisable = true
				var that=this;
				var url = kwz.project.replace('api/wx','servlet/CenterHandleServlet')
				//创建订单
				kwz.acall2({
					url : url,
					params : {
						type : 7,
						productId : that.msg.id
					},
					type : 'GET',
					success : function(ret){
						if(ret.StatusCode==200){
							that.code = ret.data.returnData.code
							
							//获取微信支付相关参数
							var attach = {
								payUserId : "${payUserId}",
								childId : id,
								termId : that.msg.termId
							}
							var jsonStr = JSON.stringify(attach)
							kwz.acall2({
								url : url,
								params : {
									type : 8,
									total : that.msg.price,
									outTradeNo : that.code,
									name : that.msg.name,
									attach : jsonStr
								},
								type : 'GET',
								success : function(ret){
									var herfs = window.location.href.split("#/");
						    	    var pathes = herfs[0].substring(0,herfs[0].lastIndexOf("/")+1)
						    		
						    		var pathname = window.location.pathname;
						    		var projectName = pathname.substring(1,pathname.substring(1).indexOf('/') + 1)
						    		
						    		var json={time:new Date().getTime()};
						    		window.history.pushState(json,"",pathes + herfs[1].split("?")[0]);
						    		setTimeout(function(){
										function onBridgeReady(data,that){
											WeixinJSBridge.invoke(
													'getBrandWCPayRequest', {
														"appId" : data.appId,     //公众号名称，由商户传入     
														"timeStamp":data.timeStamp,         //时间戳，自1970年以来的秒数     
														"nonceStr" : data.nonceStr, //随机串     
														"package" : data.packageValue,     
														"signType" : data.signType,         //微信签名方式：     
														"paySign" : data.paySign //微信签名 
													},
													function(res){
														that.isDisable = false
														if(res.err_msg == "get_brand_wcpay_request:ok" ) {
																alert('支付成功') 
																window.location.href='http://trtxkt.qgjydd.com/jxt/static/wx/index.html#/main'  								
														}else{     // 使用以上方式判断前端返回,微信团队郑重提示：res.err_msg将在用户支付成功后返回    ok，但并不保证它绝对可靠。
															alert("支付失败，请稍后再试");
														}
													}
											); 
										}
										
										if (typeof WeixinJSBridge == "undefined"){
											if( document.addEventListener ){
												document.addEventListener('WeixinJSBridgeReady', onBridgeReady(ret.data,that), false);
											}else if (document.attachEvent){
												document.attachEvent('WeixinJSBridgeReady', onBridgeReady(ret.data,that)); 
												document.attachEvent('onWeixinJSBridgeReady', onBridgeReady(ret.data,that));
											}
										}else{
											onBridgeReady(ret.data,that);
										}
									},1300)
								}
							})
						}else{
							alert("网络异常，稍后重试！");
						}
					},
				})
				/*var url = kwz.project + '/setJf'
				kwz.acall2({
					url : url,
					params : null,
					type : 'GET',
					success : function(data){
						if(data.StatusCode==200){
							alert(data.data.errinfo);
						}else{
							alert("网络异常，稍后重试！");
						}
					},
				})*/
			}
		}
	}
</script>

<style scoped>
	select{
		/* flex: 2; */
		background-color: #fff;
		width: 4.6rem;
		border-radius: 0.1rem;
		font-size: 0.35rem;
		height: 0.73rem;
		padding-left: 0.2rem;
		background-image: url('../../gzxtkt/images/ico_17.png');
		background-size: 0.3rem 0.1rem;
		background-repeat: no-repeat;
		background-position: 4rem center;
	}
	.sel{
		width: 100%;
		height: 1.25rem;
		border-bottom: 1px solid #e7e7e7;
		border-top: 1px solid #e7e7e7;
		display: flex;
		/* background-color: orange */
	}
	.sel span{
		line-height: 1.25rem;
		margin-left: 0.32rem;
		font-size: 0.3rem;
		color: #000;
		padding: 0;
	}
	.jf{
		color: red!important;
		font-weight: 900;
		font-size: 0.3rem;
	}
	.ytj{
		/* margin-top: 0.3rem; */
		border: 0px;
		width: 100%;
		height: 1rem;
		border-radius: 1rem;
		background-color: #ADADAD;
		color: #fff;
		font-size: 0.35rem;
	}
	.ytj:active{
		opacity: .5;
	}
	.jImg{
		flex: 0.2;
		width: 100%;
		height: 1.04rem;
		/* background-color: orange; */
		padding: 0 0.3rem;
		box-sizing: border-box;
	}
	.jMsg{
		flex: 0.8;
		width: 100%;
		height: 1.04rem;
		/* background-color: red; */
		padding: 0 0.3rem 0 0;
		box-sizing: border-box;
	}
	.jMsg a{
		font-size: 0.35rem;
		color: #000;
	}
	.info{
		overflow: hidden;
		width: 100%;
		padding: 0.35rem 0.45rem;
	}
	.img{
		float: left;
		width: 1.6rem;
		height: 1.6rem;
		border-radius: 100%;
		background-color: #fff;
		margin: 0 auto;
		padding: 0.055rem 0.055rem;
	}
	.img img{
		width: 1.5rem;
		height: 1.5rem;
		border-radius: 100%;
	}
	.name{
		padding-left: 2rem;
		margin-top: .8rem;
		font-size: 0.35rem;
		color: #fff;
		font-weight: 500;
	}
	.school{
		padding-left: 2rem;
		margin-top: 0.2rem;
		font-size: 0.33rem;
		color: #fff;
	}
	.teachinfo{
		margin-top: 0.35rem;
		display: flex;
		flex-flow: row;
		width: 100%;
		height: 1rem;
	}
	.t_i{
		flex: 1;
	}
	.t_i p:nth-child(1){
		margin-top: 0.1rem;
		font-size: 0.33rem;
		text-align: center;
		color: #fff;
	}
	.t_i p:nth-child(2){
		margin-top: 0.15rem;
		font-size: 0.33rem;
		text-align: center;
		color: #fff;
	}
	.log-off{
		text-align: center;
	}
	
	.main1{
		/* margin-top: 3rem; */
		width: 100%;
		margin-bottom: 1.3rem;
	}
	.main1 p{
		line-height: 0.34rem;
		display: block;
		width: 100%;
		padding: 0.44rem 0.7rem 0.44rem 0rem;
		background-color: #fff;
		font-size: 0.3rem;
		color: #333333;
		border-bottom: 0.01rem solid #e7e7e7;
		background-size: 0.15rem 0.27rem;
		background-repeat: no-repeat;
		background-position: 7.05rem center;
		box-sizing: border-box;
	}
	.groud{
		float: left;
		margin-right: 0.3rem;
		width: 0.33rem;
		height: 0.33rem;
		background-size: 100% 100%;
	}
	
     .count0{
     	 margin-top: 10rem;
		float: right;
		color: #666666;
	}
	.count{
		float: right;
		color: #666666;
	}
	.tj{
		/* margin-top: 0.3rem; */
		border: 0px;
		width: 100%;
		height: 1rem;
		border-radius: 1rem;
		background-color: #40cc8b;
		color: #fff;
		font-size: 0.35rem;
	}
	.tj:active{
		opacity: .5;
	}
	.title{
		font-weight: bold;
    	color: #000;
    	float: right;
   		margin-top: .3rem;
   		font-size: .31rem;
	}
	.rmb{
		color: red;
    	margin-left: 0.62rem;
    	font-size: .4rem;
    	margin-top: 0.1rem;
    	/* border-bottom: 0.01rem solid #e7e7e7; */
	}
	.title_img{
		width:25%;
		border-radius: 3px;
	}
</style>


