<template>
	<div>
		<header>
	       <img @click="back" class="fan" src="../../gzxtkt/images/xs_pic_fh.png"/>
			<p>密码修改</p>
		</header>
		<div class="main">
			<div class="tx">
				<div></div>
				<input type="password"  name="" v-model="oldp" placeholder="请输入当前密码" @focus="clearInput">
			</div>
			<div class="tx">
				<div></div>
				<input type="password" name="" v-model="newp"  placeholder="请输入新密码" @focus="clearInput">
			</div>
			<div class="tx">
				<div></div>
				<input type="password" name="" v-model="surep" placeholder="确认新密码" @focus="clearInput">
			</div>
			<div class="mmgs">密码为6-10位数字或字母组合</div>
			<div class="tsxx"></div>
			<div class="btn">
				<div><button @click="submit">确认</button></div>
			</div>
		</div>
	</div>
</template>
<script>
	import kwz from '../utils/kwz.js'
	export default {
		data() {
			return {
				oldp:'',
				newp:'',
				surep:'',
			}
		},
		methods:{
			clearInput(){
				$(".tsxx").html("");
			},
			submit(){
				if(this.oldp==''){
					$(".tsxx").html("原密码不能为空！");
					return;
				}else if(this.newp==''){
					$(".tsxx").html("请输入新密码！");
					return;
				}else if(this.surep==''){
					$(".tsxx").html("请输入确认密码！");
					return;
				}else if(this.newp!=this.surep){
					$(".tsxx").html("两次密码的输入不一致，请从新输入！");
					return;
				}
				var that=this;
                var url=kwz.project+'/editMyPassWord?opassword='+this.oldp+'&npassword='+this.newp;
                kwz.acall2({
                    url:url,
                    params:null,
                    type:'GET',
                    success:function(res){
                        if (res.StatusCode==200) {
                        	if (res.data.status=="fail") {
                        		alert(res.data.errinfo)
                        		that.clear();
                        	}else{	
	                            alert(res.data.errinfo)
	                            history.go(-1)
                        	}
                        }  
                    }
                });

			},
			clear(){
				this.oldp='';
				this.newp='';
				this.surep='';
			},
			back() {
				history.go(-1)
			},
			
		}
	}
</script>
<style scoped>
	.fan{
		position: absolute;
		left: 0.25rem;
		top: 0.26rem;
		width: 0.37rem;
		height: 0.35rem;
	}
	.main{
		width: 100%;
	    height: 100%;
	    top: -0.12rem;
	    /* margin-top: 1rem; */
	    background-color: #e7e7e7;
	    position: fixed;
	}
	.xgmm{
	    line-height: .2rem;
	    padding: .3rem;
	    font-weight: bold;
	    font-size: .32rem;
	    display: flex;
	}
	.tx img{
		display: block;
	    width: .9rem;
	    height: .9rem;
	    border-radius: 100%;
	    margin: 0 auto;
	    position: relative;
	    margin-right: 0.2rem;
	    margin-top: .1rem;
	}
	.tx{
		width: 100%;
	    display: -webkit-box;
	    display: -ms-flexbox;
	    display: flex;
	    position: relative;
	    top: .3rem;
	}
	.tx div{
	    width: 5%;
	    text-align: center;
	    height: 1rem;
	    border-bottom: 1px solid #e7e7e7;
	    background-color: #f3f3f3;
	    border-radius: 0;
	}
	.tx input{
	    height: 1rem;
	    background-color: #f3f3f3;
	    border: none;
	    width: 100%;
	    font-size: .32rem;
	    border-bottom: 1px solid #e7e7e7;
	    border-radius: 0;
	}
	.xgmm div{
		position: fixed;
	    right: 15px;
	    margin-top: -4px;
	}
	.tscl{
		margin-top: .2rem;
	}
	.btn{
		display: flex;
	}
	.btn button{
		width: 5rem;
	    height: .8rem;
	    position: relative;
	    margin-top: 1.2rem;
	    margin-left: 1.2rem;
	    font-size: .32rem;
	    border-radius: 20px;
	    border: 1px solid;
	    background-color: #0caedf;
	    color: #fff;
	}
	.tsxx{
		color: red;
	    margin-top: .5rem;
	    margin-bottom: -.5rem;
	    margin-left: .5rem;
	    font-size: .32rem;
	}
	.main .tx:nth-child(3){
		margin-top: .2rem;
	}
	.mmgs{
		color: #adadad;
	    position: relative;
	    margin-top: .5rem;
	    margin-left: .3rem
	}
</style>


