<template>
	<div>
		<header>
	        <router-link to='/' class="goback"></router-link>
			<p>班级相册</p>
		</header>
		<div class="dc"></div>
		<!-- 班级列表 -->
		<div class="className">
			<p>{{nj}}</p>
			<router-link :to="'/classList/bjkj?classId='+classID">
				<div class="className_list" v-for="(item,v) in classList" :key="v" @click="cList(v)">
					<div class="className_img">
						<img style="width:1.19rem;height:1.19rem;border-radius: 0.1rem;" src="../../gzxtkt/images/xcList.png" alt="图片">
					</div>
					<div class="className_msg">
						<p style="color:#000;font-size:0.32rem;margin-top:0.1rem;">{{item.claname}}</p>
						<span><i>{{item.acou}}</i>个相册</span>
						<span><i>{{item.pcou}}</i>张照片</span>
						<span><i>{{item.vcou}}</i>个视频</span>
					</div>
				</div>
			</router-link>
		</div>
	</div>
</template>
<script>
	import kwz from '../utils/kwz.js'
	export default {
		data() {
			return {
				classList:[],
				nj:'',
				classID:[],
			}
		},
		created:function() {
			if(!kwz.getClaAlbumList){
				let that=this;	
				var url_bj = kwz.project + '/getClaAlbumList'
				kwz.acall2({
					url : url_bj,
					params : null,
					type : 'GET',
					success : function(data){
						if(data.StatusCode==200){
							that.classList = data.data;
							that.nj = data.data[0].nj;
							kwz.getClaAlbumList=data.data;
						}else{
							alert("网络异常，稍后重试！");
						}
					},
				 });
			}else{
				this.classList=kwz.getClaAlbumList;
				this.nj=kwz.getClaAlbumList[0].nj;
			}
		},
		methods: {
			cList(v) {
				//添加班级iD
				this.classID.push(this.classList[v].claid);
				// 添加班级名字
				this.classID.push(this.classList[v].claname);
			}
		},
	}
</script>
<style scoped>
	.dc{
		position: absolute;
		top: 0px;
		bottom: 0px;
		width: 100%;
		background-color: #fff;
		z-index: 0;
	}
	.className {
		position: relative;
		top: 0.88rem;
		padding: 0.3rem;
		box-sizing: border-box;
	}
	.className p{
		font-size: 0.35rem;
		color: #1d91e8;
		margin-bottom: 0.1rem;
	}
	.className_list{
		width: 100%;
		height: 1.6rem;
		/* background-color: orange; */
		padding: 0.2rem 0;
		display: flex;
		border-top: 1px solid #eee;
	}
	.className_img{
		flex: 0.2;
		width: 100%;
		height: 1.2rem;
		/* background-color: red; */
	}
	.className_msg{
		flex: 0.8;
		width: 100%;
		height: 1.2rem;
		/* background-color: #ccc; */
	}
	.className_msg{
		color: #50585a;
	}
</style>


