<template>
	<div>
		<header>
	       <router-link to='/main' class="goback"></router-link>
			<p>编辑资料</p>
		</header>
		<div class="main">
			<!-- <router-link to='/sczpho'> -->
				<div class="tx" @click="uplmage">
					<p>头像</p>
					<img v-if="upImgUrl!=''" :src="upImgUrl">
					<img v-else-if="heardUrl!=''" :src="heardUrl"  @error="findError">
					<img v-else src="../../gzxtkt/images/xcList.png">
				</div>
			<!-- </router-link>	 -->
			<router-link to='/editPassword'>
				<div class="xgmm">
					<p>密码更改</p>
					<div><Icon type="chevron-right"></Icon></div>
				</div>
			</router-link>
		</div>
	</div>
</template>
<script>
	import kwz from '../utils/kwz.js'
	export default {
		data() {
			return {
				heardUrl:'',
				imgurl:'',
				upImgUrl:'',
			}
		},
		created:function() {
		    var that = this
   			this.imgurl= (kwz.project+"/").replace("api/wx/","");
   			if(kwz.getMyInfo && kwz.getMyInfo!="undefined"){
	               this.heardUrl=this.imgurl+'s'+kwz.getMyInfo.photo;
	        }else{
	            var url=kwz.project+'/getMyInfo';
	            kwz.acall2({
	                url:url,
	                params:null,
	                type:'GET',
	                success:function(res){
	                    if (res.StatusCode==200) {
	                    	if(res.data.photo && res.data.photo!="undefined"){
	                         that.heardUrl=that.imgurl+'s'+res.data.photo;
	                    	}else{
	                    		that.heardUrl='';
	                    	}
	                    }  
	                }
	            });
	        }
		},
		methods:{
			uplmage(){
				var that=this;
				wx.ready(function(){
					wx.chooseImage({
					    count: 1, // 默认9
					    sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
					    sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
					    success: function (res) {
				        	var localIds = res.localIds; // 	返回选定照片的本地ID列表，localId可以作为img标签的src属性显示图片
				        	that.uploadImage(localIds[0])

	    				},
	    				fail: function (res) {
		                   	alert("加载失败,请点击右上角刷新后重试！");
		                }
					});
				})
			},
			uploadImage(localId){
				var that=this;
				wx.uploadImage({
				    localId: localId, // 需要上传的图片的本地ID，由chooseImage接口获得
				    isShowProgressTips: 1, // 默认为1，显示进度提示
				    success: function (res) {
				        var serverId = res.serverId; // 返回图片的服务器端ID
				        that.downloadImage(serverId);
				    }
				});
			},
			downloadImage(serverId){
				alert("serverId="+serverId);
				var that=this;
				var url=kwz.project+'/uploadWxHead?mediaId='+serverId;
				kwz.acall2({
					url:url,
					type:'GET',
					params:null,
					success:function(res){
						if(res.data.url && res.data.url!="undefined"){	
						    that.upImgUrl=that.imgurl+'s'+res.data.url;
						}else{
							that.upImgUrl='';
						}
						that.editPhoto(res.data.url);
						// alert(res.data.errinfo);
					}
				});
			},
			 editPhoto(imgUrl){
                var that=this;
                var url=kwz.project+'/editMyphoto?photo='+imgUrl;
                kwz.acall2({
                    url:url,
                    params:null,
                    type:'GET',
                    success:function(res){
                        if (res.StatusCode==200) {
                        	kwz.getMyInfo='';
                            alert(res.data.errinfo)
                        }  
                    }
                });
            },
            findError(){
				this.heardUrl="../../gzxtkt/images/xcList.png";
			}
		}
	}
</script>
<style scoped>
	.main{
		width: 100%;
	    height: 100%;
	    border-bottom: 1px solid #ccc;
	    /*position: fixed;*/
	    top: -0.12rem;
	    background-color: #fff;
	}
	.xgmm{
	    line-height: .2rem;
	    /*padding: .3rem;*/
	    font-weight: bold;
	    font-size: .32rem;
	    display: flex;
	    color: #657180;
	}
	.tx img{
		display: block;
	    width: 1rem;
	    height: 1rem;
	    border-radius: 100%;
	    margin: 0 auto;
	    position: relative;
	    margin-right: 0.2rem;
	    margin-top: .1rem;
	}
	.tx{
		color: #657180;
		display: flex;
		border-bottom: 2px solid #eee;
		height: 1.2rem;

	}
	.tx p{
		font-size: .32rem;
	    font-weight: bold;
	    padding: .3rem;
	    line-height: .2rem;
	    margin-top: .2rem;
	}
	.xgmm div{
		position: relative;
	    right: -5.2rem;
	    margin-top: -4px;
	}
	.xgmm p{
		position: relative;
	    margin-left: .3rem;
	    margin-top: -.1rem;
	}
</style>


