<template>
	<div>
		<header style="position:relative">
			<!-- <router-link to='' class="goback" ></router-link> -->
			<img @click="back" class="fan" src="../../gzxtkt/images/xs_pic_fh.png"/>
			<p>图片</p>
			<div v-if="utype==1" class="bjxc">
				<Dropdown trigger="click" placement="bottom-end">
					<!-- <a href="javascript:void(0)"> -->
						<Icon type="plus-round"></Icon>
					<!-- </a> -->
					<!-- <Icon type="plus-round"></Icon> -->
					<Dropdown-menu slot="list" >
						<Dropdown-item style="display: flex;">
		            	<div class="aa"><img class="imgbj" :src="url_img"></div>
		            	<router-link :to="'/sczp?xcid='+aid">
		            		<div class="wzbj" >上传照片</div>
		            	</router-link>
		            	</Dropdown-item>
						<Dropdown-item style="display: flex;">
							<div class="aa"><img class="imgbj" src="../../gzxtkt/images/glxc.png"></div>
							<a><div class="wzbj" @click="sel">管理照片</div></a>
						</Dropdown-item>
						<Dropdown-item style="display: flex;">
								<div class="aa"><img class="imgbj" src="../../gzxtkt/images/bjxc.png"></div>
							    <router-link :to="'/classList/bjkj/photo/update?name='+bj">
								<div @click="bjxc" class="wzbj" >编辑相册</div>
							</router-link>
						</Dropdown-item>
					</Dropdown-menu>
				</Dropdown>
			</div>
		</header>
		

		<div class="dc"></div>
		<!-- 班级图片 -->
		<div class="classPhoto">
			<div v-if="lastphoto==''" class="classPhoto_L" @click="subimt">
				<img class="classImg" src="../../gzxtkt/images/xcList.png" alt="班级图片">
			</div>
			<div v-else class="classPhoto_L" @click="subimt">
				<img class="classImg" :src="imgurl+lastphoto" alt="班级图片">
			</div>
			<div style="display: flex;width: 100%;" @click="subimt">
				<div class="classPhoto_M" >
					<h4>&nbsp;&nbsp;{{lx}}</h4>
					<p><i style="color:red">&nbsp;&nbsp;{{w}}</i> 张照片</p>
					<span>&nbsp;&nbsp;记录精彩每一天</span>
					<!-- <img class="classPhoto_Img" src="../../gzxtkt/images/wd_09.png" height="27" width="16" alt=""> -->
				</div>
				<div class="icon_xc"><Icon type="chevron-right"></Icon></div>
			</div>
			<!-- //删除相册 -->
			<div class="classPhoto_R">
				<Button @click="scxc" v-if="hide" type="error" style="margin-top: 0.28rem;">删除相册</Button>
			</div>
		</div>
		<!-- 相册列表 -->

		<div class="photoList">

			<div class="photoList_img" v-for="(item,v) in imgList" :key="v" @click="chak(v)">
				<input type="checkbox" :id='"ab"+v' :value='item.pid' v-model='list'>
				<label :for='"ab"+v'>
					<img @click="previewImgUrl(v)" id="img" :src="imgurl+'z'+item.path" style="width:100%;height:100%;z-index:1;"/>
					<a class="shan" v-if="hide" style="color:white;font-size:0.5rem">
						<Icon type="ios-checkmark-outline"></Icon>
					</a>
					<div class="hide" v-if="hide"></div>
				</label>
			</div>
			
		</div>
		<!-- 点击加载更多 -->
		<div v-if="w>=10" @click="jia" class="jia"><Icon type="ios-plus-outline"></Icon></div>
		<!-- 管理照片 -->
		<div class="gjzp" v-if="hide" >
			<div class="gjzp_L">
				<span>已选择 <i style="color:red;">{{num}}</i> 张照片</span>
			</div>
			<div class="gjzp_R">
				<i @click="del">删除</i>&nbsp;&nbsp; 
				<span @click="tui">退出管理</span>
			</div>
		</div>
		<!-- 弹窗 -->
		<div id="tan">
			<div class="tan_msg">
				<i class="close" @click="close">X</i>
				<div class="miao"><span>相册描述</span></div>
				<div class="xcmsg">
					<div>
						<p>相册描述：</p>
						<span>{{albumDetails.aintro}}</span>
					</div>
					<div>
						<p>创建时间：</p>
						<span>{{albumDetails.crtime}}</span>
					</div>
					<div>
						<p>更新时间：</p>
						<span>{{albumDetails.lasttime}}</span>
					</div>
				</div>
			</div>
		</div>
		<!-- 查看照片 -->
		<!-- <div id="zp">
			<div class="close1" @click="closezk">
				<Icon type="ios-arrow-left"></Icon> 
				<i style="font-size:0.40rem;">返回</i>
			</div>
			<div>
				<img :src="chakphto" alt="" style="width:100%;height:100%;">
			</div>
		</div> -->
		<!-- 遮罩层 -->
		<div id="zhe"></div>
	</div>
</template>
<script>
	import kwz from '../utils/kwz.js'
	import { Toast } from 'mint-ui';
	export default {
		data() {
			return {
				hide:false,
				show:false,
				list:[],
				num:0,
				imgurl:'',
				imgList:[],
				w:'',
				arrs:[],
				lx:'',
				aid:'',
				index:0,
				chakphto:'',
				page:1,
				bj:[],
				url_img:'../../gzxtkt/images/upload-pic.png',
				lastphoto:'',
				utype:1,
				albumDetails:[],
			}
		},
		created:function() {
			this.lastphoto=this.$route.query.lastphoto;
			this.utype=this.$route.query.utype;
			this.arrs=(this.$route.query.xcid).split(",");
			this.imgurl= (kwz.project+"/").replace("api/wx/","");
			for (var i = 0; i < this.arrs.length; i++) {
				this.lx = this.arrs[1];
				this.aid = this.arrs[0];
			};
			if(!kwz.getPhotoList || this.aid!=kwz.aid){
				kwz.aid=this.aid;
				var url_xc = kwz.project + '/getPhotoList?aid='+this.aid;
				var that = this;
				kwz.acall2({
					url : url_xc,
					params : null,
					type : 'GET',
					success : function(data){
						if(data.StatusCode==200){
							that.imgList = data.data.T1;
							that.w = that.imgList.length;
							kwz.w=that.imgList.length;
							kwz.getPhotoList=data.data;
						}else{
							alert("网络异常，稍后重试！");
						}
					},
				});
			}else{
				this.imgList=kwz.getPhotoList.T1;
				this.w=kwz.w;
			}
		},
		
		watch: {
			list:function() {
				this.num = this.list.length;
			}
		},
		methods: {
			previewImgUrl(v){
				var current=this.imgurl+"s"+this.imgList[v].path;
				var arrUrls=[];
				for (var i = 0; i < this.imgList.length; i++) {
					arrUrls.push(this.imgurl+"s"+this.imgList[i].path)
				}
				wx.previewImage({
				  current: current, // 当前显示图片的http链接
				  urls: arrUrls // 需要预览的图片http链接列表
			    });
			},
			//相册描述
			subimt() {
				$('#tan').show(100);
				$('#zhe').show();
				var that=this;
				var url=kwz.project+'/getAlbumDetails?aid='+this.aid;
				kwz.acall2({
					url:url,
					params:null,
					type:'GET',
					success:function(res){
						if (res.data.status=="fail") {
							alert(res.data.errinfo)
						}else{
							that.albumDetails=res.data[0];
						}	
					}
				});

			},
			//查看照片
			chak(v) {
				if (this.index == 0) {
					$('.photoList_img').eq(v).find('input')[0].checked == false;
					this.list=[];
				}else{
					if ($('.photoList_img').eq(v).find('input')[0].checked) {
						$('.photoList_img').eq(v).find('.shan')[0].style.color='red';
					}else{
						$('.photoList_img').eq(v).find('.shan')[0].style.color='#fff';
					}
				}
			},
			// 关闭相册描述
			close() {
				$('#tan').hide();
				$('#zhe').hide();
			},
			//关闭查看照片
			// closezk(){
			// 	// $('#zp').hide();
			// 	$('#zhe').hide();
			// },
			//选择
			sel(name){
				this.hide = true;
				this.index = 1;
			},
			//编辑相册
			bjxc(){
				this.bj.push(this.lx);
				this.bj.push(this.aid);
			},
			//删除照片
			del() {
				if(this.list==''){
					alert("请选择要删除的照片！")
					return;
				}else{	
					var r=confirm("是否要删除照片");
				}
				if(r == true){
					var url_xc = kwz.project + '/delPhoto?aid='+this.aid+'&pidlist='+this.list.join(',');
					var that = this;
					kwz.acall2({
						url : url_xc,
						params : null,
						type : 'GET',
						success : function(data){
							if(data.StatusCode==200){
				                history.go(0)
							}else{
								alert("网络异常，稍后重试！");
							}
						},
					});

					var url_xc1 = kwz.project + '/getPhotoList?aid='+this.aid;
					kwz.acall2({
						url : url_xc1,
						params : null,
						type : 'GET',
						success : function(data){
							if(data.StatusCode==200){
								that.imgList = data.data.T1;
								that.w = that.imgList.length;
							}else{
								alert("网络异常，稍后重试！");
							}
						},
					});
					that.hide = false;
					// 删除之后将index设为初值，否则无法查看照片
					that.index = 0;
				}
				
			},
        	//退出管理
        	tui() {
        		this.hide = false;
        		this.index = 0;
        	},
        	//选择删除图片
        	xztp(v) {

        		// this.show = true
        	},
        	//返回上一级
        	back() {
        		history.go(-1)
        	},
        	//加载更多
        	jia() {
        		this.page++;
        		var that=this;
        		setTimeout(() => {
        			var url= kwz.project +'/getPhotoList?aid='+this.aid+'&page='+this.page;
        			kwz.acall2({
        				url : url,
        				params : null,
        				type : 'GET',
        				success: function(data){
        					if(data.StatusCode==200){
        						if (data.data == '') {
        							$('.jia')[0].innerHTML='没有更多啦';
        							$('.jia')[0].style.fontSize = '0.3rem';
        						}
        						// that.imgList = data.data;
								
        						that.imgList=that.imgList.concat(data.data.T1);
        						that.w = that.imgList.length;
        					}else{
        						alert("网络异常，稍后重试！");
        					}
        				}
        			}) }, 300);
        	},

        	// 删除相册
        	scxc() {
        		let d=confirm("是否要删除该相册");
        		if(d == true){
        			var url_sc = kwz.project + '/delAlbum?aid='+this.aid;
        			var that = this;
        			kwz.acall2({
        				url : url_sc,
        				params : null,
        				type : 'GET',
        				success : function(data){
        					if(data.StatusCode==200){
        						history.go(-1);
        						let instance=Toast({
        							message: '删除成功',
        							iconClass: 'icon icon-success',
        							duration: 3000
        						});
        						setTimeout(() => {
        							instance.close();
        						}, 1000);

        						// setTimeout(function(){
        						// 	history.go(-1);
        						// },1000)
        					}else{
        						alert("网络异常，稍后重试！");
        					}
        				},
        			});
        		}else{
        			
        		}
        	}
		},
	}
</script>
<style scoped>
	.jia{
		position:relative;
		text-align:center;
		font-size:1.0rem;
		padding:0.3rem;
		color:#0caedf;
		box-sizing: border-box;
	}
	.fan{
		position: absolute;
		left: 0.25rem;
		top: 0.26rem;
		width: 0.37rem;
		height: 0.35rem;
	}
	.gjzp{
		width: 100%;
		height: 0.8rem;
		background-color: #323232;
		position: fixed;
		left: 0;
		bottom: 0;
		padding: 0 0.2rem;
		z-index: 100;
		box-sizing: border-box;
	}
	.gjzp_L{
		float: left;
		font-size: 0.3rem;
		line-height: 0.8rem;
		color: white;
	}
	.gjzp_R{
		float: right;
		font-size: 0.3rem;
		line-height: 0.8rem;
		color: red;
	}
	.close{
		display: inline-block;
		width: 0.5rem;
		height: 0.5rem;
		background-color: #40cc8b;
		border-radius: 50%;
		text-align: center;
		line-height: 0.55rem;
		font-size: 0.30rem;
		color: white;
		position: absolute;
		top: -0.20rem;
		right: -0.2rem;
	}
	.close1{
		width: 100%;
		font-size: 0.40rem;
		color: white;
		position: absolute;
		top: -0.2rem;
	}
	#tan{
		padding:0 1rem;
		box-sizing:border-box;
		width:100%;height:5rem;	
		position: fixed;
		top: 2rem;
		height: auto;
		z-index: 9999999999999999!important;
		display: none;
	}
	#tan .tan_msg{
		width: 100%;
		min-height: 4.5rem;
		position: relative;
		padding: 0.2rem 0;
		box-sizing: border-box;    
		background-color: white;
		border-radius: 0.2rem;

	}
	#zhe {
		width: 100%;
		height: 100%;
		background-color: #4a4b4d;
		position: fixed;
		left: 0px;
		top: 0px;
		opacity: 0.7;
		z-index: 999999;
		display: none;
	}
	.dc{
		position: absolute;
		top: 0px;
		bottom: 0px;
		width: 100%;
		background-color: #fff;
		z-index: 0;
	}
	.classPhoto{
		width: 100%;
		height: 1.6rem;
		/* background-color: orange; */
		position: relative;
		/* top: 0.88rem; */
		padding: 0.2rem;
		box-sizing: border-box;
		display: flex;

	}
	.classPhoto_L{
		flex: 0.2;
		width: 100%;
		height: 1.2rem;
		/* background-color: red; */
	}
	.classPhoto_M{
		flex: 0.6;
		width: 100%;
		height: 1.2rem;
		position: relative;
		/* background-color: red; */
	}
	.classPhoto_R{
		flex: 0.2;
		width: 100%;
		height: 1.2rem;
		/* background-color: orange; */
	}
	.classImg{
		width: 1.2rem;
		height: 1.2rem;
		border-radius: 0.1rem;
	}
	.classPhoto_M h4{
		font-size: 0.28rem;
		font-weight: 600;
		margin-top: 0.04rem;
	}
	.classPhoto_M p{
		font-size: 0.20rem;
	}
	.classPhoto_M span{
		font-size: 0.20rem;
	}
	.classPhoto_Img{
		position: absolute;
		right: 0;
		top: 0.34rem;
	}
	.photoList{
		width: 100%;
		height: auto;
		/* border: 1px solid #eee; */
		/* background-color: orange; */
		position: relative;
		/* top: 0.88rem; */
		display: flex;
		padding: 0 0.2rem 0 0;
		box-sizing: border-box;
		margin-top: 0.2rem;
		flex-wrap: wrap;
	}
	.photoList_img{
		width: 30.5%;
		height: 2.2rem;
		/* flex: 1; */
		/* background-color: orange; */
		border: 1px solid #eee;
		margin: 0 0 0.2rem 0.2rem;
		position: relative;
	}
	.photoList_img input{
		display: none;
	}
	.miao{
		width: 100%;
		height: 0.6rem;
		border-bottom: 1px solid #ececec;
		text-align: center;
		font-size: 0.32rem;
		/* margin-top:0.2rem; */
	}
	.xcmsg{
		width: 100%;
		height: auto;
		padding: 0 0.3rem;
		box-sizing: border-box;
	}
	.xcmsg div{
		margin-top: 0.2rem;
	}
	.xcmsg div p {
		font-size: 0.3rem;
		color: #b0b3b6;
		margin-bottom: 0.15rem;
	}
	.xcmsg div span {
		font-size: 0.25rem;
		color: #000;
		/* margin-bottom: 0.15rem; */
	}
	.bjxc{
		color: #fff;
		position: absolute;
		top: 0.15rem;
		right: 0.2rem;
		font-size: .40rem;
	}
	.imgbj{
		width: 80%;
	}
	.shan{
		position: absolute;
		right: 0;
		top: -0.10rem;
		z-index: 10;
	}
	.hide{
		width:100%;
		height:100%;
		background-color:#000;
		position:absolute;
		top:0;
		opacity:0.5;
	}
	.aa{
		margin-left: -0.15rem;
	}
	.aa img{
		width: 0.4rem;
	}
	.wzbj{
		color:#657180;
		margin-top: -.1rem;
	} 
	.icon_xc{
		margin-right: -3rem;
    	margin-top: .4rem;
	}


</style>


