<template>
	<div>
		<header>
			<router-link to='/' class="goback"></router-link>
			<p>发送消息</p>
		</header>
		<div class="dc"></div>

		<!-- 家长端 -->

		<div class="content" v-if="utype==3">
			<div class="lylx">
				<div class="l_s">
					<p>留言类型：</p>
					<select v-model='selected' @change="chaSel">
						<option checked value="0">请选择</option>
						<option value="4">给老师留言</option>
						
					</select>
				</div>
				<!-- 个人留言模块 -->
				<div class="l_g" v-show='gr'>
					<p>班级：</p>
					<select v-model='bj_s'>
						<option checked value="0">请选择班级</option>
						<option v-for='(bj,i) in bj_list' :value='i+1' :key='i'>{{bj.name}}</option>
					</select>
				</div>
				<!-- 加载列表 -->
				<div class="l_nj" v-show='flag' :key='key'>
					<div style="display:flex;">
						<p>选择老师(<span style="color: red;">点击选择，再点取消</span>)</p>
						<div :class="{qx_div:!isqx}" @click="selectAll"><div :class="{qx:isqx}">全选</div></div>
					</div>
					<div class="l_n_l">
						<div class="nj_list" v-for='(l, i) in options' :key='i' style="margin-bottom: 0.2rem;">
							<input type="checkbox" name="" :id='"a"+i' :value='l.uid' v-model='list'>
							<label :for='"a"+i'  @click="clickOne(l,i)">
								<p :class="{listTea:l.isChecked}">{{l.uname}}({{l.post}})</p>
							</label>
						</div>
					</div>
				</div>
			</div>

			<textarea maxlength="255" v-model='message'></textarea>
			
			<p class="tips">限255字，您已输入<span style="color: red;">{{conut}}</span>字</p>
			
			<p class="title"><i>{{shi}}</i></p>

			<div style="position:relative;">
				<input id='t_begin' type="text" name="" data-lcalendar="2000-01-01,2018-01-29" readonly class="time" placeholder="请输入定时留言时间" v-init v-model='time'>
				<i class="del" @click="delet">X</i>
			</div>
			<button class="tj" @click='_submit'>发送</button>
		</div>

		<!-- 老师端 -->

		<div class="content" v-if="utype==1">
			<div class="lylx">
				<div class="l_s">
					<p>留言类型：</p>
					<select v-model='selected' @change="chaSel">
						<option checked value="0">请选择</option>
						<option value="1">年级留言</option>
						<option value="2">班级留言</option>
						<option value="3">个人留言</option>
					</select>
				</div>
				<!-- 个人留言模块 -->
				<div class="l_g" v-show='gr'>
					<p>班级：</p>
					<select v-model='bj_s' @change="selClass">
						<option checked value="0">请选择班级</option>
						<option v-for='(bj,i) in gr_clist' :value='bj.claid' :key='i'>{{bj.claname}}</option>
					</select>
				</div>
				<!-- 加载列表 -->
				<div class="l_nj" v-show='flag' :key='key'>
					<div style="display:flex;">
						<p>选择{{title}}(<span style="color: red;">点击选择，再点取消</span>)</p>
						<div :class="{qx_div:!isqx}" @click="selectAll"><div :class="{qx:isqx}">全选</div></div>
					</div>
					<div class="l_n_l">
						<div class="nj_list" v-for='(l, i) in options' :key='i'>
							<input type="checkbox" name="" :id='"a"+i' :value='l.id' v-model='list'>
							<label :for='"a"+i'  @click="clickOne(l,i)">
								<p :class="{listTea:l.isChecked}">{{l.name}}</p>
							</label>
						</div>
					</div>
				</div>
			</div>

			<textarea maxlength="255" v-model='message'></textarea>
			
			<p class="tips">限255字，您已输入<span style="color: red;">{{conut}}</span>字</p>
			
			<p class="title"><i>{{shi}}</i></p>

			<div style="position:relative;">
				<input id='t_begin' type="text" name="" data-lcalendar="2000-01-01,2018-01-29" readonly class="time" placeholder="请输入定时留言时间" v-init v-model='time'>
				<i class="del" @click="delet">X</i>
			</div>
			<button class="tj" @click='_submit'>发送</button>
		</div>
		


	</div>
</template>

<script>
	var timer=null;
	import { Toast } from 'mint-ui';
	import kwz from '../utils/kwz.js'
	export default {
		data(){
			return {
				flag: false,
				// 选中条件
				selected: 0,

				// 年级留言列表
				nj_list: [],
				nj_list1:[],
				// 班级留言列表
				bj_list: [],

				// 个人留言
				gr: false,
				// 个人 班级列表
				gr_clist: [],
				// 选中班级
				bj_s: 0,
				shi:'定时留言,请选择时间',
				// 留言信息
				message: '',
				// 选中列表
				list: [],
				// list1:[],
				// 预定时间
				time: '',
				//年级id，班级id
				classId:'',
				//信息内容
				neirong:'',

				//短信pc
				txpc:'',

				// 老师或者家长utype
				utype:'',
				isqx:false,
				isqx_div:true,
				isall:false,
				isOne:true,
				listCurr:[]
			}
		},
		created: function(){
			var self=this;
			//发送内容
			self.neirong = self.message;

				// 请求年级列表
			var url_1 = kwz.project + '/getGradeList'
			$.ajax({
				url: url_1,
				dataType: 'json',
				type: 'GET',
				success: function(data){
					if(data.StatusCode==200&&data.data.length!=0){
						for(let i=0;i<data.data.length;i++){
							var nj={};
							nj.id=data.data[i].graid;
							nj.name=data.data[i].graname;
							self.nj_list.push(nj);
						}
					}
					if (data.data.status == "fail") {
						alert(data.data.errinfo)
					}
				}
			})
			

			//获取短信批次
			var url_5 = kwz.project + '/getMsgPc'
			$.ajax({
				url: url_5,
				dataType: 'json',
				type: 'GET',
				success: function(data){
					if(data.StatusCode==200){
						self.txpc = data.data.pc;
					}
					if (data.data.status == "fail") {
						alert(data.data.errinfo)
					}
				}
			});

			// 获取utype，判断是家长端还是老师端
			var url = kwz.project + '/getMyInfo'
			kwz.acall2({
				url : url,
				params : null,
				type : 'GET',
				success : function(data){
					if(data.StatusCode==200){
						self.utype=data.data.utype;
						self.message = data.data.uname+'的留言：';
					}else{
						alert("网络异常，稍后重试！");
					}
				},
			 });

			//获取老师  家长端
			
				var url_7 = kwz.project + '/getTeacherList'
				$.ajax({
					url: url_7,
					dataType: 'json',
					type: 'GET',
					success: function(data){
						if(data.StatusCode==200){
							self.nj_list1=data.data;
						}else{
							alert("网络异常，稍后重试！");
						}
					}
				});
			


		},
		watch: {
			time:function () {
				if (this.time != '') {
					this.shi = '时间选定';
					$('.title').css({color:"#40cc8b"})
				}
			},
			// 留言类型改变时
			selected: function(){
				this.isqx=false;
				// utype == 1
				if (this.utype == 1) {
					// 清空已选中列表
				this.list=[];
				//加载班级列表
				if (this.selected == 2) {
					//查看年级ID
					var url_2 = kwz.project + '/getClassList';
					$.ajax({
						url: url_2,
						dataType: 'json',
						type: 'GET',
						success: function(data){
							if(data.StatusCode==200&&data.data.length!=0){
								for(let i=0;i<data.data.length;i++){
									var bj={};
									bj.id=data.data[i].claid;
									bj.name=data.data[i].claname;
									self.bj_list.push(bj);
								}
							}
							if (data.data.status == "fail") {
								alert(data.data.errinfo)
							}
						}
					})
					// debugger
				}
				// 选择个人留言
				if(this.selected==3){
					var self = this;
					var url_3 = kwz.project + '/getClassList';
					$.ajax({
						url: url_3,
						dataType: 'json',
						type: 'GET',
						success: function(data){
							if (data.data == '') {
								alert('请先选择年级')
							}
							if(data.StatusCode==200){
								self.gr_clist=data.data;
							}
							if (data.data.status == "fail") {
								alert(data.data.errinfo)
							}
						}
					})
					// 先隐藏列表
					this.flag=false;
					// 将班级选择栏显示
					this.gr=true;
				}else if(this.selected==0){
					// 重置个人留言的班级选中
					this.bj_s=0;
					this.gr=false;
					this.flag=false;
				}else{
					clearTimeout(timer);
					timer=null;
					// 重置个人留言的班级选中
					this.bj_s=0;
					this.gr=false;
					var self=this;
					timer=setTimeout(function(){
						self.flag=true;
					}, 1);
				}
				}else{
					// 清空已选中列表
					this.list=[];
				// 选择个人留言
				if(this.selected==3){
					// 先隐藏列表
					this.flag=false;
					// 将班级选择栏显示
					this.gr=true;
				}else if(this.selected==0){
					// 重置个人留言的班级选中
					this.bj_s=0;
					this.gr=false;
				}else{
					clearTimeout(timer);
					timer=null;
					// 重置个人留言的班级选中
					this.bj_s=0;
					this.gr=false;
					var self=this;
					timer=setTimeout(function(){
						self.flag=true;
					}, 1);
					
					
				}
				}
			},
			// 个人留言 班级改变时
			bj_s: function(){
				this.isqx=false;
				// 清空已选中列表
				this.list=[];
				if(this.bj_s==0){
					// this.bj_s=0;
					this.flag=false;
				}else{
					this.flag=true;
				}
			}
		},
		computed: {
			conut: function(){
				return this.message.length;
			},
			// 加载年级列表/班级列表/学生列表
			options: function(){
				if (this.utype == 1) {
					// 加载年级列表
					if(this.selected==1){
						this.listCurr=this.nj_list;
						return this.nj_list;


					}
				// 加载班级列表
				if(this.selected==2){
				    this.listCurr=this.bj_list;
					return this.bj_list;
				}
				// 加载学生列表
				if(this.selected==3){
					// debugger
						// 请求班级成员列表
						// 项目调试时，claid要改成真实值！！！
						var cy_list=[];
						var url_4 = kwz.project + '/getClassMember?claid='+this.classId;
						$.ajax({
							url: url_4,
							dataType: 'json',
							type: 'GET',
							// 同步
							async: false,
							success: function(data){
								if(data.StatusCode==200){
									for(let i=0;i<data.data.length;i++){
										var cy={};
										cy.id=data.data[i].uid;
										cy.name=data.data[i].uname;
										cy.code=data.data[i].ucode;
										cy_list.push(cy);
									}
								}else{
									alert("网络异常，稍后重试！");
								}
							}
						})
						this.listCurr=cy_list;
						return cy_list;
					}
				}else{
					if(this.selected==4){
						this.listCurr=this.nj_list1
						return this.nj_list1;
					}
				}
			},
			// 标题
			title: function(){
				// 年级
				if (this.utype == 1) {
					if(this.selected==1){
						return '年级';
					}
					// 班级
					if(this.selected==2){
						return '班级';
					}
					// 个人
					if(this.selected==3){
						return '联系人';
					}
				}else{
					return 
				}
			},
			// key值 防止vue复用
			key: function(){
				// 个人
				if(this.selected==3){
					return "gr"+this.bj_s;
				}else{
					return this.selected;
				}
			}
		},
		directives: {
			init: {
				inserted: function(){
					var calendar = new lCalendar();
					calendar.init({
					    'trigger': '#t_begin',
					    'type': 'datetime'
					});
				}
			},
			check: {
				inserted: function(el){
					el.onclick=function(){
						if(el.getElementsByTagName('input')[0].checked){
							el.getElementsByTagName('p')[0].style.color='#fff';
							el.style.backgroundColor='#1bb7ff';
						}else{
							el.getElementsByTagName('p')[0].style.color='#1bb7ff';
							el.style.backgroundColor='#fff';
						}
					}
				}
			}
		},
		methods: {
			clickOne(item,i){
				if(this.isall){
					if(item.isChecked == undefined || item.isChecked){
						this.$set(this.listCurr[i],'isChecked',false)
					}else{
						this.$set(this.listCurr[i],'isChecked',true)
					}	
				}else{
					if(item.isChecked!=undefined && item.isChecked){
						this.$set(this.listCurr[i],'isChecked',false)
					}else{
						this.$set(this.listCurr[i],'isChecked',true)
					}	
				}
				var that = this
				setTimeout(function(){	
					if(that.list.length==that.listCurr.length){
						that.isqx=true;
					}else{
						that.isqx=false;
					}		
				},200)
				

			},
			selectAll(){
				if(this.isqx){
					this.isqx=false;
					this.isall=false;
					this.list=[];
					this.listCurr.forEach(function(item){
						item.isChecked = false
					})

				}else{
					this.isqx=true;
					this.isall=true;
					if(this.utype==1){
						for (var i = 0; i < this.listCurr.length; i++) {
							this.list.push(this.listCurr[i].id)
						}
					}else{	
						for (var i = 0; i < this.listCurr.length; i++) {
							this.list.push(this.listCurr[i].uid)
						}
					}
					this.listCurr.forEach(function(item){
						item.isChecked = true
					})
				}
			},
			//清空时间
			delet () {
				if (this.time != '') {
					this.time = '';
					this.shi = '定时留言,请选择时间';
					$('.title').css({color:"#000"})
				}else{
					
				}
			},			
			// 监听模块变化
			chaSel (v) {
				this.neirong = this.message;
				if(v.target.value) {
					this.classId = this.list.join(',');
				}
			},
			// 监听个人留言班级
			selClass (v) {
				this.classId = v.target.value;
			},
			// 、、提交
			_submit: function(){
				kwz.getOutboxList='';
				if (this.utype == 1) {
				var self=this;
				// 选中列表成员id
				var ids='';
				// 是否有预定时间
				var isds=0;
				var time='';
				//发送内容
				if(this.time!=''){
					isds=1;
					time=this.time+":00";
				}
				if(true){
					for(let i=0;i<this.list.length;i++){
						if(i!=this.list.length-1){
							ids+=this.list[i]+',';
						}else{
							ids+=this.list[i];
						}
					}

					// 提交接口地址
					var url='';
					var sendMsg=encodeURI(self.message);
					var par=null;
					if(this.selected==1){
						par={'msgtype':self.selected,'isds':isds,'sendtime':time,'gralist':ids,'msg':sendMsg,'pc':self.txpc}
					}else if(this.selected==2){
						par={'msgtype':self.selected,'isds':isds,'sendtime':time,'clalist':ids,'msg':sendMsg,'pc':self.txpc}
					}else if(this.selected==3){
						par={'msgtype':self.selected,'isds':isds,'sendtime':time,'claid':self.bj_s,'stulist':ids,'msg':sendMsg,'pc':self.txpc}
					}else if(this.selected==0){
						par=null
					}
					if (self.neirong == sendMsg) {
						Toast('信息内容不能为空')
					}else{
						var url_1 = kwz.project + '/insertMsg'
					kwz.acall2({
						url:url_1,
						dataType: 'json',
						type: 'POST',
						params:par,
						success: function(data){
							if (data.StatusCode==200) {
								if(data.data.status == "success"){
										Toast({
											message: '发送成功',
											iconClass: 'icon icon-success',

										});
										setTimeout(function(){
											history.go(-1);
										},3000)	
								}else{
									alert(data.data.errinfo)
								}	
								
							}						
						},
						
					});
					}
									
				}
				}else{
				let seTime = '';
				let isds = 0;
				let msg = this.message;
				let nei = this.neirong;//初始内容
				let Tid = this.list.join(',');
				let sel=this.selected;
				let pc=this.txpc;
				//是否定时发送
				if(this.time != ''){
					seTime = this.time + ':00';
					isds = 1
				}
				if (nei == msg) {
					Toast('信息内容不能为空')
				}else{
				    var sendMsg=encodeURI(msg);
					var url_12 = kwz.project + '/insertMsg'
					kwz.acall2({
						url:url_12,
						dataType: 'json',
						type: 'POST',
						params:{'msgtype':sel,'isds':isds,'sendtime':seTime,'tealist':Tid,'msg':sendMsg,'pc':pc},
					    success: function(data){
							if (data.data.status == "success") {					
									Toast({
										message: '发送成功',
										iconClass: 'icon icon-success',

									});
									setTimeout(function(){
										history.go(-1);
									},3000)
							}else{
								alert(data.data.errinfo);
							}
					 }
				});
				}
				
				}
			}
		}
	}
</script>

<style scoped>
	.del{
		position:absolute;right:0;top:0.20rem;
		display: inline-block;
		width: 0.6rem;
		height: 0.6rem;
		/* background-color: orange; */
		text-align: center;
		line-height:0.60rem;
		font-size: 0.33rem;
		color: red;
		border-radius: 0.08rem;
	}
	.del:active{
		background-color: #40cc8b;
		color: white;
	}
	/* 弹窗 */
	/* .tan{

	}
 */
	.dc{
		position: absolute;
		top: 0px;
		bottom: 0px;
		width: 100%;
		background-color: #fff;
		z-index: 2;
	}
	.content{
		position: relative;
		z-index: 3;
		margin-top: 0.88rem;
		width: 100%;
		background-color: #fff;
		padding: 0.3rem;
	}
	.lylx{
		width: 100%;
		border: 0.01rem solid #e7e7e7;
		border-radius: 0.1rem;
	}
	.l_s,.l_g{
		display: flex;
		flex-flow: row;
		padding: 0.2rem;
	}
	.l_s p,.l_g p{
		flex: 1;
		font-size: 0.35rem;
		color: #111111;
		line-height: 0.8rem;
	}
	.l_s select,.l_g select{
		flex: 2;
		background-color: #fff;
		width: 4.6rem;
		border-radius: 0.1rem;
		font-size: 0.35rem;
		padding-left: 0.2rem;
		background-image: url('../../gzxtkt/images/ico_17.png');
		background-size: 0.3rem 0.1rem;
		background-repeat: no-repeat;
		background-position: 4rem center;
	}
	.l_nj{
		padding: 0.2rem;
		width: 100%;
		border-top: 0.01rem solid #e7e7e7;
	}
	.l_nj p{
		font-size: 0.3rem;
		color: #222222;
	}
	textarea{
		width: 100%;
		height: 3rem;
		border: 0.01rem solid #e7e7e7;
		margin-top: 0.3rem;
		resize: none;
		border-radius: 0.1rem;
		padding: 0.2rem;
		font-size: 0.33rem;
		line-height: 0.4rem;
	}
	.listTea{
    	background-color: #1bb7ff;
    	color: #fff!important;
    }
	.tips{
		float: right;
		font-size: 0.33rem;
		margin-top: 0.1rem;
		color: #111111;
	}
	.title{
		clear: both;
		font-size: 0.3rem;
		color: #222222;
		margin-top: 0.8rem;
		line-height: 0.45rem;
	}
	.title:before{
		content: '';
		width: 0.1rem;
		border-left: 0.1rem solid #1bb7ff;
		height: 0.45rem;
		margin-right: 0.2rem;
	}
	.time{
		width: 100%;
		height: 1rem;
		border: 0px;
		border-bottom: 0.01rem solid #e7e7e7;
		padding-left: 0.2rem;
		font-size: 0.33rem;
	}
	.tj{
		margin-top: 0.3rem;
		border: 0px;
		width: 100%;
		height: 1rem;
		border-radius: 1rem;
		background-color: #0cacde;
		color: #fff;
		font-size: 0.35rem;
	}

	/*加载列表*/
	.l_n_l{
		width: 100%;
		display: flex;
		flex-wrap: wrap;
	}
	.nj_list{
		border: 0.01rem solid #1bb7ff;
		border-radius: 0.1rem;
		background-color: #fff;
		transition: background-color .5s;
		margin-right: 0.2rem;
		margin-top: 0.2rem;
	}
	.nj_list input{
		display: none;
	}
	.nj_list p{
		font-size: 0.3rem;
		padding: 0.2rem 0.2rem;
		color: #1bb7ff;
		transition: color .5s;
	}

	/*个人留言*/
	.l_g{
		border-top: 0.01rem solid #e7e7e7;
	}
	.tj:active{
		opacity: .5;
	}
	.qx{
    	background-color: #0cacde;
	    margin-left: .8rem;
	    color: #fff;
	    width: .8rem;
	    text-align: center;
	    padding-top: .04rem;
	    border-radius: 5px;
	    z-index: 99999999;
    }
    .qx_div{
    	/*background-color: #0cacde;*/
	    margin-left: .8rem;
	    color:  #0cacde;
	    width: .8rem;
	    text-align: center;
	    padding-top: .04rem;
	    border-radius: 5px;
	    border: 1px solid  #0cacde;
    }
</style>


