<template>
	<div>
		<header>
			<router-link to='/' class="goback"></router-link>
			<p>亲情短信</p>
		</header>
		<div class="dc"></div>
		<div class="content">
		<router-link to=/xzfsdx>
			<div class="lylx">
			  	<div class="xzfz">选择联系人</div>
			  	<div v-if="length_num>0" class="selecteNum">已选择{{length_num}}个人</div>
			  	<div class="icon_right"><Icon type="chevron-right"></Icon></div>
			</div>
		</router-link>
			<div class="srhm">
			  	<input type="text" name="" placeholder="   输入号码：" v-model="mobile">
			</div>

			<textarea maxlength="255" v-model='message'></textarea>
			
			<p class="tips">限255字，您已输入<span style="color: red;">{{conut}}</span>字</p>
			
			<p class="title"><i>{{shi}}</i></p>

			<div style="position:relative;">
				<input id='t_begin' type="text" name="" data-lcalendar="2000-01-01,2018-01-29" readonly class="time" placeholder="请输入定时留言时间" v-init v-model='time'>
				<i class="del" @click="delet">X</i>
			</div>
			<button class="tj" @click='_submit'>发送</button>
		</div>
	</div>
</template>

<script>
	var timer=null;
	import { Toast } from 'mint-ui';
	import kwz from '../utils/kwz.js'
	export default {
		data(){
			return {
				flag: false,
				shi:'定时留言,请选择时间',
				// 留言信息
				message: '',
				time: '',
				txpc:'',
				utype:'',
				model1:'',
				tids:'',
				idss:[],
				length_num:0,
				sids:'',
				mobile:''
			}
		},
		created: function(){
			//防止在当前页面刷新
			if(kwz.getMyInfo && kwz.getMyInfo!="undefined" && kwz.getMyInfo!=''){
				this.message=kwz.getMyInfo.uname+"的留言：";
			}else{
				var that=this;
				var url = kwz.project + '/getMyInfo'
				kwz.acall2({
					url : url,
					params : null,
					type : 'GET',
					success : function(data){
						if(data.StatusCode==200){
							that.message=data.data.uname+"的留言：";
						}else{
							alert("网络异常，稍后重试！");
						}
					},
			    })
			}
			this.sids = this.$route.query.ids;
			if(this.sids==''||this.sids=='undefined' || !this.sids){
				this.length_num=0;
			}else{
				this.idss=this.sids.split(",");
				this.length_num=this.idss.length;
			}
			var that=this;
			var url_5 = kwz.project + '/getMsgPc'
			$.ajax({
				url: url_5,
				dataType: 'json',
				type: 'GET',
				success: function(data){
					if(data.StatusCode==200){
						that.txpc = data.data.pc;
					}
					if (data.data.status == "fail") {
						alert(data.data.errinfo)
					}
				}
			});
		},
		watch: {
			time:function () {
				if (this.time != '') {
					this.shi = '时间选定';
					$('.title').css({color:"#40cc8b"})
				}
			},
		},
		computed: {
			conut: function(){
				return this.message.length;
			},
		},
		directives: {
			init: {
				inserted: function(){
					var calendar = new lCalendar();
					calendar.init({
					    'trigger': '#t_begin',
					    'type': 'datetime'
					});
				}
			},
		},
		methods: {
			//清空时间
			delet () {
				if (this.time != '') {
					this.time = '';
					this.shi = '定时留言,请选择时间';
					$('.title').css({color:"#000"})
				}else{
					
				}
			},
			_submit: function(){
				if(this.mobile!=''){
				    if(!(/^1[34578]\d{9}$/.test(this.mobile))){ //验证手机号码
				        alert("手机号码有误，请重新输入！");  
				        return false; 
				    }
				}

				kwz.getOutboxList='';
				var self=this;
				// 选中列表成员id
				var ids='';
				// 是否有预定时间
				var isds=0;
				var time='';
				//发送内容
				if(this.time!=''){
					isds=1;
					time=this.time+":00";
				}
				// 提交接口地址
				var url='';
				var sendMsg=encodeURI(self.message);
				var par={'msgtype':15,'isds':isds,'sendtime':time,'tealist':self.sids,'msg':sendMsg,'pc':self.txpc,'mobile':self.mobile};
					
				if (self.neirong == sendMsg) {
					Toast('信息内容不能为空')
				}else{
					var url_1 = kwz.project + '/insertMsg'
					kwz.acall2({
						url:url_1,
						dataType: 'json',
						type: 'POST',
						params:par,
						success: function(data){
							if (data.StatusCode==200) {
								if(data.data.status == "success"){
										Toast({
											message: '发送成功',
											iconClass: 'icon icon-success',

										});
										setTimeout(function(){
											history.go(-3);
										},3000)	
								}else{
									alert(data.data.errinfo)
								}	
								
							}						
						},
						
					});
				}
			},
			
		}
	}
</script>

<style scoped>
	.del{
		position:absolute;right:0;top:0.20rem;
		display: inline-block;
		width: 0.6rem;
		height: 0.6rem;
		/* background-color: orange; */
		text-align: center;
		line-height:0.60rem;
		font-size: 0.33rem;
		color: red;
		border-radius: 0.08rem;
	}
	.del:active{
		background-color: #40cc8b;
		color: white;
	}
	/* 弹窗 */
	/* .tan{

	}
 */
	.dc{
		position: absolute;
		top: 0px;
		bottom: 0px;
		width: 100%;
		background-color: #fff;
		z-index: 2;
	}
	.content{
		position: relative;
		z-index: 3;
		margin-top: 0.88rem;
		width: 100%;
		background-color: #fff;
		padding: 0.3rem;
		overflow-x: hidden;
	}
	.lylx{
		display: flex;
	    border: 0.01rem solid #e7e7e7;
	    border-radius: 0.1rem;
	    height: 1rem;
	    margin-top: -0.4rem;
	    position: relative;
	}
	
	textarea{
		width: 100%;
		height: 3rem;
		border: 0.01rem solid #e7e7e7;
		margin-top: 0.3rem;
		resize: none;
		border-radius: 0.1rem;
		padding: 0.2rem;
		font-size: 0.33rem;
		line-height: 0.4rem;
	}
	.tips{
		float: right;
		font-size: 0.33rem;
		margin-top: 0.1rem;
		color: #111111;
	}
	.title{
		clear: both;
		font-size: 0.3rem;
		color: #222222;
		margin-top: 0.8rem;
		line-height: 0.45rem;
	}
	.title:before{
		content: '';
		width: 0.1rem;
		border-left: 0.1rem solid #1bb7ff;
		height: 0.45rem;
		margin-right: 0.2rem;
	}
	.time{
		width: 100%;
		height: 1rem;
		border: 0px;
		border-bottom: 0.01rem solid #e7e7e7;
		padding-left: 0.2rem;
		font-size: 0.33rem;
	}
	.tj{
		margin-top: 0.3rem;
		border: 0px;
		width: 100%;
		height: 1rem;
		border-radius: 1rem;
		background-color: #0cacde;
		color: #fff;
		font-size: 0.35rem;
	}
	.tj:active{
		opacity: .5;
	}

    .xzfz{
		font-size: .32rem;
	    position: relative;
	    top: .2rem;
	    /*font-weight: bold;*/
	    margin-left: .3rem;
	    color: rgba(153, 153, 153, 0.88);
    }
    .selecteNum{
    	font-size: .28rem;
	    color: rgba(153, 153, 153, 0.88);
	    top: 0.22rem;
	    right: .5rem;
	    position: absolute;
	    width: 1.8rem;
    }
   .icon_right{
   	    color: #0cacde;
	    font-size: .4rem;
	    margin-left: 4.5rem;
	    margin-top: .15rem;
   }
   .srhm input{
   	    width: 100%;
	    border: 1px solid #e7e7e7;
	    border-radius: .1rem;
	    height: 1rem;
	    font-size: .32rem;
	    margin-top: -.09rem;
   }
</style>


