<template>
	<div>
	    <header>
	        <router-link to='/yftz' class="goback"></router-link>
			<p>已收信息详情</p>
		</header>
		<div class="main">
        <table>
        	<tr>
        		<td>序号</td>
        		<td>姓名</td>
        		<td>微信接收</td>
        		<td>短信接收</td>
        	</tr>
        	<tr v-for='(item,v) in detailsMsgs'>
      			<td>{{v+1}}</td>
      			<td>{{item.uname}}</td>
      			<td>
      				<div v-if="item.wxres==1">成功</div>
      				<div v-else-if="item.wxres==''">无状态</div>
      				<div v-else>失败</div>
      			</td>
      			<td>
      				<div v-if="item.smsres==1">成功</div>
      				<div v-else-if="item.smsres==''">无状态</div>
      				<div v-else>失败</div>
      			</td>
      		</tr>
        </table>
		</div>
	</div>
</template>


<script>
import kwz from '../utils/kwz.js'
	export default {
		data(){
			return {
				detailsMsgs: [],
			}
		},
		created: function(){
			    var pcid=this.$route.query.demo;
			    var that=this;
				var url = kwz.project + '/getMsgStatus?pcid='+pcid;
				kwz.acall2({
					url : url,
					params : null,
					type : 'GET',
					success: function(data){
						if(data.StatusCode==200){
							that.detailsMsgs=data.data;
						}else{
						alert("网络异常，稍后重试！");
						}
					}
				})
		},
	}
</script>


<style scoped>
	.main{
		word-wrap: break-word;
		margin-top: 1rem;
		width: 100%;
		margin-bottom: -0.7rem;
		border-bottom: 1px solid #d0d0d0;
	}
	table tr{
		font-size: .3rem;
    	text-align: center;
   	 	height: .8rem;
    	border-bottom: 1px solid #0caedf;
	}
	table td:nth-child(1){
		text-align: center;
		height: .8rem;
		width: 15%;
	}
	table td:nth-child(2){
		text-align: center;
		height: .8rem;
		width: 25%;
	}
	table td:nth-child(3){
		text-align: center;
		height: .8rem;
		width: 30%;
	}
	table td:nth-child(4){
		text-align: center;
		height: .8rem;
		width: 30%;
	}
	table{
		width: 100%;
	}
</style>




