<template>
	<div id="app">
	   <!--  <ul class="container">
	        <li v-for="img in imgs" :key="img"><img :src="img.img"></li>
	    </ul> -->
	 <div class="container">
	    <!-- <Carousel autoplay >
	        <Carousel-item v-for="img in imgs" :key="img">
	            <img :src="img.img">
	        </Carousel-item>
	    </Carousel> -->
	    <mt-swipe :auto="4000">
		  <mt-swipe-item v-for="img in imgs" :key="img"><img :src="img.img"></mt-swipe-item>
		</mt-swipe>
	</div>

	</div>
</template>

<style scoped>
   .container{
		position: relative;
		width: 100%;
		height: 3rem;
		overflow: hidden;
		z-index: 2;
	}
	.container img{
		width: 100%;
		height: 3rem;
	}
	.mint-swipe-indicator {
	    width: 8px;
	    height: 8px;
	    display: inline-block;
	    border-radius: 100%;
	    background: #000;
	    opacity: 0.5!important;
	    margin: 0 3px;
	}
</style>

<script>
import kwz from '../utils/kwz.js'
	export default {
		data(){
			return {
				imgs: []
			}
		},
        // mounted: function () {
        //     this.startChange();
        // },
        //获取轮播图片
        created: function(){
        	if(!kwz.getAdImageList){
				let that=this;
				var url = kwz.project + '/getAdImageList'
				kwz.acall2({
					url : url,
					params : null,
					type : 'GET',
					success: function(data){
						if(data.StatusCode==200){
								that.imgs = data.data;
								kwz.getAdImageList=data.data;
							}else{
								alert("网络异常，稍后重试！");
						}
					}
				})
        	}else{
        		this.imgs=kwz.getAdImageList;
        	}
		},
        methods: {
            // startChange: function () {
            //     setInterval(this.next, 2500);
            // },
            // next: function () {
            //     this.imgs.push(this.imgs.shift());
            // }
        }
	}
</script>


