var kwz = {
	project : 'http://trtxkt.qgjydd.com/jxt/api/wx',
	/***
	* @param url 请求地址
	* @param params 请求参数；类型为JSON格式 {name : '张三',age : 18}
	* @param cbk 回调函数
	*
	*/
	acall : function(url,params,cbk){
		var data = params
		$.ajax({
		   type: "GET",
		   url: url,
		   // dataType : "JSON",
		   data : data,
		   async : true,
		   success : function(ret){
		     	if(typeof cbk == 'function')
		     		cbk(ret)
		   	// }
		   },
		   error : function(ret){
		   	debugger
		   	 if(typeof cbk == 'function'){
		   	 	var data = eval("(" + ret.responseText + ")")
		   	 	cbk(data)
		   	 }
		   }

		})
	},
	/***
	* @param url 请求地址
	* @param params 请求参数；类型为JSON格式 {name : '张三',age : 18}
	* @param cbk 回调函数
	*
	*/
	acall2 : function(data){
		if(!data.url){
			console.log('url 不能为空')
			return
		}
		var params = {}
		var method = "GET"
		var type = "json"
		if(data.params){
			params = data.params
		}
		// if(data.method){
		// 	method = data.method
		// }
		if(data.type){
			method = data.type
		}
		$.ajax({
		   type: method,
		   url: data.url,
		   // dataType : type,
		   data : params,
		   async : true,
		   success : function(ret){
		   if(data.success && (typeof data.success == 'function')){
		   		if(data.params)ret.params=params;
	     		data.success(ret)
			}
		   },
		   error : function(ret){
		   	 if(data.error && typeof data.error == 'function'){
		   	 	var datas = eval("(" + ret.responseText + ")")
		     		data.error(datas)
		   	 }
		   }

		})
	},
	isIE : function(){
		return ("ActiveXObject" in window)
	},
	isChrome : function(){
		var ua = navigator.userAgent.toLowerCase();
		var index = ua.indexOf('chrome')
		return index != -1
	},
	formatDate : function(dates){
		if(dates == '')
			return ''
		return dates.split(' ')[0]
	},
	date2Str : function(date){
		if(date){
			var month = parseInt(date.getMonth() + 1) > 9 ? date.getMonth() + 1 : '0' + parseInt(date.getMonth() + 1);
			var day = date.getDate() > 9 ? date.getDate() : '0' + date.getDate();
			return (date.getFullYear() + '-' + month + '-' + day)	
		}
		else
			return ""
	}

	
}
module.exports = kwz


