<template>
	<div>
		<header>
	        <router-link to='/classList' class="goback"></router-link>
			<p>选择相册</p>
			<div v-if="utype==1" class="bjxc">
				<Dropdown trigger="click" placement="bottom-end">
					<Icon type="plus-round"></Icon>
					<Dropdown-menu slot="list">
		            	<!-- <Dropdown-item style="display: flex;">
		            	<div class="aa"><img class="imgbj" :src="url_img"></div>
		            	<router-link to="/sczp">
		            		<div class="wzbj" >上传照片</div>
		            	</router-link>
		            	</Dropdown-item> -->
		            	<Dropdown-item style="display: flex;">
		            	<div class="aa"><img class="imgbj" src="../../gzxtkt/images/scsp.png"></div>
		            	<a><div class="wzbj" @click="modal1 = true">上传视频</div></a>
		            	</Dropdown-item>
		            	<Dropdown-item style="display: flex;">
		            	<div class="aa"><img class="imgbj" src="../../gzxtkt/images/new-pic.png"></div>
		            	<router-link :to="'/newAlbum?newXc='+classID"><div class="wzbj">新建相册</div></router-link>
	            	   </Dropdown-item>
       			 </Dropdown-menu>
				</Dropdown>
			</div>
		</header>
		<div class="dc"></div>
		<div class="shcoolNmae">
			<div class="shcoolImg">
				<img :src="Img" alt="" style="width: 1.0rem;height: 1.0rem;border-radius: 0.15rem;">
			</div>
			<div class="shoolMsg">
				<p>{{className}}</p>
				<a></a>
				<span>0.00G/5.00G</span>
			</div>
		</div>
		<div>
		<mt-loadmore :bottomDistance=70 :bottomLoadingText="null" :bottomDropText="null" :bottomPullText="null" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" >
			<div class="albumList">
				<div >
				<router-link :to="'/classList/bjkj/Video?vId='+classID+'&utype='+utype">
					<div  class="Video">
						<div>
							<img :src="shImg" alt="" style="width:100%;height:2.84rem;">
						</div>
						<div class="prompt clear">
							<span>视频收藏</span>
							<strong>{{countall_sp}}</strong>
						</div>
					</div>

				</router-link>
			</div>	
					
			<div v-for="(item,v) in xc">			
				<router-link :to="'/classList/bjkj/photo?xcid='+xcId+'&lastphoto='+item.lastphoto+'&utype='+utype">						
					<div class="photo">
						<div @click="xcid(v)">
							<div v-if="item.lastphoto==''">
								<img src="../../gzxtkt/images/xcList.png" alt="" style="width:100%;height:2.84rem;">
							</div>
							<div v-else>
								<img :src="imgurl+item.lastphoto" alt="" style="width:100%;height:2.84rem;">
							</div>
							<div class="prompt clear">
								<span>{{item.aname}}</span>
								<strong>{{item.photonum}}</strong>							
							</div>	
						</div>		
					</div>					
				</router-link>
			</div>
			</div>	
		</mt-loadmore>			
		</div>
		<!-- 上传视频 -->
		<Modal v-model="modal1" title="上传视频" ok-text='' cancel-text=''>
	        <div class="uploadMaterial" id="uploadMaterial" @change="show_path">
			    <form id= "uploadForm_sp" > 
			        <div class="fileUpload">
			           	<div class="file" id="changeFile">选择视频
			           		<input class="qwer" id="file" type="file" name="file"/>
			           	</div>
			           	<div class="upload" @click="subimtBtnsp">上传视频</div>
			        </div>
			    </form>
			    <div v-if="spin_show">
			        <div class="scIcon">上传中,请稍后……</div>    
        		</div>
			    <div class="hidens" id="sp_path"></div>
		    </div>
        </Modal>
	</div>
</template>
<script>
	import kwz from '../utils/kwz.js'

	export default {
		data () {
			return {
				Img:'../../gzxtkt/images/xcList.png',
				shImg:'../../gzxtkt/images/playList.png',
				className:'',
				classID:'',
				xc:[],
				imgurl:'',
				arrs:[],
				xcId:[],
				countall_sp:0,
				countall:0,
				modal1:false,
				// imgurl:'',
				spin_show:false,
				page: 1,
				pagesize: 10,
				allLoaded:false,
				url_img:'../../gzxtkt/images/upload-pic.png',
				utype:1,

			}
		},
		created:function() {
			this.arrs=(this.$route.query.classId).split(",");
			this.imgurl= (kwz.project+"/").replace("api/wx/","");
			//获取班级和班级ID
			for (var i = 0; i < this.arrs.length; i++) {
				this.className = this.arrs[1];
				this.classID = this.arrs[0];
			};
			//请求班级相册，视频
			var that = this;
			if(!kwz.getAlbumList){
				var url_xc = kwz.project + '/getAlbumList?claid='+this.classID;
				kwz.acall2({
					url : url_xc,
					params : null,
					type : 'GET',
					success : function(data){
						if(data.StatusCode==200){
							kwz.getAlbumList=data.data;
							that.xc = data.data.T1;
							that.countall=data.data.T0[0].countall;
						}else{
							alert("网络异常，稍后重试！");
						}
					},
				 });
			}else{
				this.xc=kwz.getAlbumList.T1;
				this.countall=kwz.getAlbumList.T0[0].countall;
			}
			if(!kwz.getVideoList){
				var url_1 = kwz.project + '/getVideoList?claid='+this.classID;
				kwz.acall2({
					url : url_1,
					params : null,
					type : 'GET',
					success : function(data){
						if(data.StatusCode==200){
							that.countall_sp=data.data.T0[0].countall;
							kwz.getVideoList=data.data;
						}else{
							alert("网络异常，稍后重试！");
						}
					},
				 });
			}else{
				this.countall_sp=kwz.getVideoList.T0[0].countall;
			}

			if(!kwz.getMyInfo){
				var url_my = kwz.project + '/getMyInfo'
				kwz.acall2({
					url : url_my,
					params : null,
					type : 'GET',
					success : function(data){
						if(data.StatusCode==200){
							that.utype=data.data.utype;
							kwz.getMyInfo=data.data;
						}else{
							alert("网络异常，稍后重试！");
						}
					},
				 });
			}else{
				this.utype=kwz.getMyInfo.utype;
			}
		},
		methods:{
			loadBottom(){//上拉分页
				if(this.page>this.countall/this.pagesize){
					 this.allLoaded = true;// 若数据已全部获取完毕
	  				 this.$refs.loadmore.onBottomLoaded();
				}	
			   	this.page++;
			   	var that=this;
			   	setTimeout(() => {
				var url= kwz.project +'/getAlbumList?claid='+this.classID+'&page='+this.page;
				kwz.acall2({
					url : url,
					params : null,
					type : 'GET',
					success: function(data){
						if(data.StatusCode==200){
							that.xc=that.xc.concat(data.data.T1);
							
						}else{
							alert("网络异常，稍后重试！");
						}
					}
				}) }, 2000);
	        },
			xcid(v){
				this.xcId.push(this.xc[v].aid);
				this.xcId.push(this.xc[v].aname)
			},
			subimtBtnsp(){
			  kwz.getVideoList='';
			  this.spin_show=true;
			  var fileName=$( "#file" ).val();
			  var videoStyle=[".rm", ".rmvb", ".wmv", ".avi",".mp4",".3gp",".mkv",".MOV",];
			  var flag=false;
			  if(fileName.length==0){
        			alert("请先选择要上传的文件！");
        			// this.modal1=false;
        			this.spin_show=false;
        			return;
        		}else{	
	        		for(var i=0;i<videoStyle.length;i++){
	        		  	if(fileName.toUpperCase().indexOf(videoStyle[i].toUpperCase())>=0){
	        		  		flag=true;
	        		  	}
	        		 }
	        		  	if(!flag){
	        		  		this.spin_show=false;
	        		  		$( "#file" ).html("");
	        		  		this.modal1=false;
	        		  		alert("视频格式不支持上传！");
	        		  		return;
	        		 }
        	    }
			  var that=this;	
			  var formData = new FormData($( "#uploadForm_sp" )[0]);
	          $.ajax({  
	                  url:  'http://trtxkt.qgjydd.com/jxt/api/uploadFile?type=video', 
	                  type: 'POST',  
	                  data: formData,  
	                  async: true,  
	                  cache: false,  
	                  contentType: false,  
	                  processData: false,  
	                  success: function (returndata) { 
	                 	 that.addVideo(returndata.data[0].url,returndata.data[0].title,returndata.data[0].fieSize);
	                 	 that.spin_show=false;

	                  },  
	                  error: function (returndata) { 
	                      alert("上传失败！");  
	                  }  
	             });

			},
			addVideo(x,y,z){
				// this.imgurl= (kwz.project).replace("api/wx/","");
				var that=this;
				var url = kwz.project + '/addVideo?pintro='+y+'&path='+x+'&claid='+that.classID+'&vsize='+z; 
                kwz.acall2({
                    url : url,
                    params : null,
                    type : 'GET',
                    success : function(data){
                        if(data.StatusCode==200){
                        	$( ".hidens" ).html(""); 
                        	that.countall_sp+=1;
                        	that.modal1=false;
                        	alert(data.data.errinfo);
                        }
                    },
                });
			},
			show_path(){
				var filenames=$( "#file" ).val();
	        	var fileName=filenames.substring(filenames.lastIndexOf("\\")).substring(1);
	        	$("#sp_path").html('<p class="display_path">'+fileName+'</p>');
			},
	
		}
	}
</script>
<style>
	.bjxc{
		color: #fff;
		position: absolute;
		top: 0.15rem;
		right: 0.2rem;
		font-size: .40rem;
	}
	.imgbj{
		width: 85%;
	}
	.dc{
		position: absolute;
		top: 0px;
		bottom: 0px;
		width: 100%;
		background-color: #fff;
		z-index: 0;
	}
	.shcoolNmae{
		width: 100%;
		height: auto;
		display: flex;
		/* background-color: orange; */
		position: relative;
    	top: 0.88rem;
    	padding: 0.2rem;
    	box-sizing: border-box;
    	border-bottom: 1px solid #d9d9d9;
    	margin-bottom: 0.2rem;

	}
	.shcoolImg{
		width: 1.0rem;
		height: 1.0rem;
		margin-right: 0.2rem;
		/* background-color: red; */
		/* border-radius: 0.15rem; */
	}
	.shoolMsg{
		width: 100%;
		height: 1.0rem;
		/* background: orange; */
	}
	.shoolMsg p{
		font-size: 0.30rem;
		color: #d9d9d9;
		margin-top: 0.1rem;
	}
	.shoolMsg a{
		display: inline-block;
		width: 2.5rem;
		height: 0.12rem;
		background-color: #d9d9d9;
		border-radius: 0.1rem;
	}
	.shoolMsg span{
		font-size: 0.2rem;
	}
	.albumList{
		width: 100%;
		padding: .8rem 0 0rem 0.2rem;
    	box-sizing: border-box;
    	display: flex;
    	flex-wrap: wrap;
	}
	.Video, .photo{
		/* flex: 1; */
		width: 3.25rem;
		height: 3.5rem;
		/* border: 1px solid #d9d9d9; */
		border-radius: 0.10rem;
		/* margin-right: 0.2rem; */
		background-color: white;
		box-shadow: #ccc 3px 2px 10px;
		position: relative;
		border-radius: 0.2rem;
		margin-top: 0.2rem;
		margin-right: 0.2rem;
		float: left;
	}
	/* .Video{
		margin-right: 0.2rem;
	} */
	.prompt{
		width: 100%;
		height: 0.6rem;
		background-color: white;
		position: absolute;
		bottom: 0;
		left: 0;
		padding: 0 0.2rem;
		box-sizing: border-box;

	}
	.prompt span{
		font-size: 0.3rem;
		float: left;
		line-height: 0.6rem;
		color: #4d4d4d;
		/* text-align: left; */

	}
	.prompt strong{
		font-size: 0.3rem;
		float: right;
		line-height: 0.6rem;
		color: #dd1d1d;
		/* text-align: right; */
	}
	.clear:after{
		content: '';
		display: block;
		clear: both;
	}
	.uploadMaterial{
	/*background-color: #39f;*/
	width: 100%;
    height: 2.4rem;
    /*background: #12B7F5;*/
    position: absolute;
    z-index: 300000;
    left: 50%;
    transform: translate(-50%,-50% );
    /*bottom: 15%;*/
    word-wrap: break-word;
	margin-top: 1rem;
	margin-bottom: -0.7rem;
	/*border-bottom: 1px solid #d0d0d0;*/
	 }
	 .file {
		float:left;
	    position: relative;
	    display: inline-block;
	    /*padding: 2rem 1.1rem;*/
	    color: #39f;
	    text-decoration: none;
	    text-indent: 0;
	    /*line-height: 12px;*/
	    width: 50%;
	    text-align: center;
	    color: #666;
	    font-weight: bold;
	    font-size: 15px;
	}
	.upload{
		float:left;
	    /*padding: 2rem 1.1rem;*/
	    color: #39f;
	    text-decoration: none;
	    color: #666;
	    text-indent: 0;
	    /*line-height: 12px;*/
	    width: 50%;
	    text-align: center;
	    font-weight: bold;
	    font-size: 15px;
	}
	.file input {
	    position: absolute;
	    opacity: 0;
	    width: 80%;
	    margin-top: -.1rem;
	    margin-left: -2.3rem;
	} 
	.hidens {
	    background-color: #fff;
	    width: 100%;
	    padding: .4rem;
	    margin-top: .57rem;
	    border-radius: 10px;
	}
	.display_path{
		color: #ADADAD;
	    float: left;
	    word-wrap: break-word;
	    width: 100%;
	    margin-top: -0.5rem;
	}
	.wzbj{
		margin-top: -.1rem;
		color: #657180;
	}
		.aa{
		margin-left: -0.15rem;
	}
	.aa img{
		width: 0.4rem;
	}
	.mint-loadmore{
		height: 11.5rem;
	}
	.scIcon{
		color: #39f;
	    position: absolute;
	    top: -1rem;
	    z-index: 9999999;
    	left: 2.6rem;
	}

</style>


