<template>
	<div style="overflow: auto;">
		<!-- <header>
			<p>我的个人资料</p>
		</header> -->
		<div class="top">
			<div class="info">
				<p class="name">我的个人资料</p>
				<router-link to="/editMy">
					<p class="bj"><a>编辑</a></p>
				</router-link>
					<div v-if="!photo && photo==''" class="img">
						<img @click="previewImgUrl" src="../../static/images/xcList.png">
					</div>
					<div v-else class="img">
						<img @click="previewImgUrl" :src="photo" @error="findError">
					</div>
				<p class="name">{{msg_main.uname}}</p>
				<div class="qhzh" v-if="msg_main.rolecou>1">
					<router-link to='/qhyh'>
						<p class="bjys">切换帐号&nbsp;<Icon type="chevron-right"></Icon></p>
					</router-link>
				</div>
			</div>
		</div>

       	<div class="main">
			<p><span class="groud1"></span>学校<span class="count">{{msg_main.schname}}</span></p>
			<p><span class="groud2"></span>帐号<span class="count">{{msg_main.username}}</span></p>
			<p v-if="utype==3 && paytype==1"><router-link to='/jfDetails'><span class="groud"></span>缴费<span class="count">&nbsp;&nbsp;&nbsp;<Icon type="chevron-right" style="color: #39f"></Icon>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span></router-link></p>
			<p><span class="groud3"></span>短信电话<span class="count">{{msg_main.smsmobile}}</span></p>
			<p><span class="groud4"></span>联系电话<span class="count">{{msg_main.mobile}}</span></p>
			<p><span class="groud5"></span>电子邮箱<span class="count">{{msg_main.email}}</span></p>
			<p><router-link to="tzzj" style="color: #464c5b;margin-left: -.1rem;"><span class="groud6"></span>联系客服<span style="margin-left: 4.3rem"><Icon type="chevron-right"></Icon></span></router-link></p>
			<div @click="outlogin" style="padding: 0 0.2rem;margin-top:0.15rem">
				<button class="tj">更改登录账号</button>
			</div>
		</div>

		<foot :show="2"></foot>
	</div>
</template>

<script>
	import f from './footer.vue'
	import kwz from '../utils/kwz.js'
	export default {
		components: {
			'foot': f // 添加底部导航组件
		},
		data(){
			return {
				msg_main: [],
				utype:'',
				photo:'',
				imgurl:'',
				paytype:''
			}
		},
		created: function(){
			this.imgurl= (kwz.project+"/").replace("api/wx/","");
			if(!kwz.getMyInfo){
				var that=this;
				var url = kwz.project + '/getMyInfo'
				kwz.acall2({
					url : url,
					params : null,
					type : 'GET',
					success : function(data){
						if(data.StatusCode==200){
							that.msg_main=data.data;
							if(data.data.photo && data.data.photo!='undefined'){
								that.photo=that.imgurl+'s'+data.data.photo;
							}
							that.utype=data.data.utype;
							that.paytype=data.data.paytype;

							kwz.getMyInfo=data.data;
						}else{
							alert("网络异常，稍后重试！");
						}
					},
			    })
			}else{
				this.msg_main=kwz.getMyInfo;
				// console.log(this.msg)
				// this.photo=this.imgurl+'s'+kwz.getMyInfo.photo;
				if(kwz.getMyInfo.photo && kwz.getMyInfo.photo!='undefined'){
					this.photo=this.imgurl+'s'+kwz.getMyInfo.photo;
					// console.log(this.photo)
				}
			}
		},
		methods:{
			previewImgUrl(){
				var current=this.photo;
				if(this.msg_main.photo==''){
		            current="../../static/images/xcList.png";
				}
				var arrUrls=[];
				arrUrls.push(this.photo)
				wx.previewImage({
				  current: current, // 当前显示图片的http链接
				  urls: arrUrls// 需要预览的图片http链接列表
			    });
			},
			outlogin(){
				window.js.logout();
			},
			findError(){
				this.photo="../../static/images/xcList.png";
			}
		}
	}
</script>

