<template>
	<div>
		<header>
	        <img @click="back" class="fan" src="../../gzxtkt/images/xs_pic_fh.png"/>
			<p>视频专辑</p>
		</header>
		<div class="video_p" v-if="sps_list.length==0"><span class="msgNull"><Icon style="color: #39f" type="videocamera"></Icon></Icon>&nbsp;&nbsp;&nbsp;暂无上传视频</span></div>
		<div v-else class="video_p">
		<mt-loadmore :bottomDistance=70 :bottomLoadingText="null" :bottomDropText="null" :bottomPullText="null" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" >
			<div class="splb" v-for="(item,v) in sps_list">
				<div class="line_b">
					<div class="video_list">
						<div class="show_spimg">
							<a :href="imgurl+item.path"><img class="imgIcon" src="../../gzxtkt/images/splb.png"></a>
							<p class="p1">{{item.pintro}}</p>
						</div>
						<div style="flex:1;">
							<p class="p2">{{item.size}}</p>
						</div>
						<div class="cz" style="flex:1;">
							<a v-if="utype==1" @click="delete_sp(v)">删除</a>
							<a :href="imgurl+item.path">下载</a>
						</div>
					</div>
				</div>
			</div>
		   </mt-loadmore>
		</div>
	</div>
</template>
<script>
	import kwz from '../utils/kwz.js'
	export default {
		data() {
			return {
				sps_list:[],
				claid:'',
				imgurl:'',
				page: 1,
				countall: 0,
				pagesize: 10,
				allLoaded:false,
				utype:1,

			}
		},
		created: function(){
			this.claid = this.$route.query.vId;
			this.utype = this.$route.query.utype;
			this.imgurl= (kwz.project+"/").replace("api/wx/","");
			var that=this;
			if(!kwz.getVideoList){
				var url = kwz.project + '/getVideoList?claid='+this.claid;
				kwz.acall2({
					url : url,
					params : null,
					type : 'GET',
					success : function(data){
						if(data.StatusCode==200){
							that.sps_list=data.data.T1;
							that.countall=data.data.T0[0].countall;
							kwz.getVideoList=data.data;
						}else{
							alert("网络异常，稍后重试！");
						}
					},
				 })
			}else{
				this.sps_list=kwz.getVideoList.T1;
				this.countall=kwz.getVideoList.T0[0].countall;
			}
		},
		methods:{
			delete_sp(v){
				kwz.getVideoList='';
				var that=this;
				var url = kwz.project + '/delVideo?vid='+this.sps_list[v].id;
				kwz.acall2({
					url : url,
					params : null,
					type : 'GET',
					success : function(data){
						if(data.StatusCode==200){
							alert(data.data.errinfo);
							that.sps_list.splice(v,1);
						}else{
							alert("网络异常，稍后重试！");
						}
					},
				 })
			},
			back(){
				history.go(-1);
			},
			loadBottom(){//上拉分页
				if(this.page>this.countall/this.pagesize){
					 this.allLoaded = true;// 若数据已全部获取完毕
	  				 this.$refs.loadmore.onBottomLoaded();
				}	
			   	this.page++;
			   	var that=this;
			   	setTimeout(() => {
				var url= kwz.project +'/getVideoList?claid='+this.claid+'&page='+this.page;
				kwz.acall2({
					url : url,
					params : null,
					type : 'GET',
					success: function(data){
						if(data.StatusCode==200){
							that.sps_list=that.sps_list.concat(data.data.T1);
							
						}else{
							alert("网络异常，稍后重试！");
						}
					}
				}) }, 2000);
	            // this.$refs.loadmore.onTopLoaded();
	        },
		}

	}
</script>
<style>
	.video_list{
		width: 100%;
		display: flex;
		padding: .1rem;
		height: 1.37rem;

	}
	video{
		  /*height: .5rem;*/
	}
	.imgIcon{
		width: .5rem;
		height: .5REM;
	}
	.video_list p{
		/*padding: .1rem;*/
		word-wrap: break-word;
		width: 78%;
	}
	.video_p{
		overflow: auto;
		margin-top: .88rem;
	    width: 100%;
	    height: 100%;
	    background-color: #fff;
	    /*position: fixed;*/
	}
	.p1{
		width: 55%;
	}
	.p2{
		 margin-left: .6rem;
	}
	a{
		padding: .1rem;
		width: .8rem;
	}
	.line_b{
		border-bottom: 1px solid #ADADAD;
	}
	.cz{
		/*right: 0;*/
	    /*margin-right: .1rem;*/
	    /*padding: 2.2rem -1.9rem;*/
	    margin-top: -.5rem;
	    flex: 0.25;
	    width: 100%;
	    font-size: 0.3rem;
	    line-height: 1.37rem;
	    /*background-color: red;*/
	}
	.show_spimg{
	    /*margin-right: .1rem;*/
	    /*padding: 2.2rem -1.9rem;*/
	    /*margin-top: .1rem;*/
	    display: flex;
	    /*flex: 3;*/
	    width: 50%;
	    flex-wrap: wrap;

	}
	.fan{
		position: absolute;
		left: 0.25rem;
		top: 0.26rem;
		width: 0.37rem;
		height: 0.35rem;
	}
	.mint-loadmore {
      height: 100%;
    }
    .msgNull{
		font-size: 0.32rem;
	    padding-left: 2.5rem;
	    width: 100%;
	    height: 100%;
	    position: fixed;
	    background-color: #fff;
	    padding-top: .5rem;

	}
</style>


