<template>
	<div>
		<header>
			<router-link to='/main' class="goback"></router-link>
			<p>联系客服</p>
		</header>
<!-- 		<div class="dc"></div>  -->
				<mt-loadmore :bottomDistance=70 :bottomLoadingText="null" :bottomDropText="null" :bottomPullText="null" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" >
		<div class="content">
			<Tabs value="name1">
				<Tab-pane label="联系客服" name="name1">
					<p style="margin-bottom: 0.2rem;">留言内容：</p>
					<div @click="clearInput"><Input  id="sendMsg" v-model="sendMsg" type="textarea" :rows="8" placeholder="请输入..."></Input></div>
					<p style="font-size:0.25rem;margin-top: 0.1rem;">最多输入200字</p>
					<div style="display: flex;">
						<Checkbox v-model="single">是否希望回复</Checkbox>
						<div class="tsxx"></div>
					</div>
					<button class="tj" @click="subimt">发送</button>
				</Tab-pane>
				<Tab-pane  label="查看已发信息" name="name2" >
					<div v-if="msgs.length==0" class="msgNull"><Icon style="color: #39f" type="information-circled"></Icon>&nbsp;&nbsp;&nbsp;暂无已发信息</div>
					<div class="main" v-else >
						<div class="list" v-for="(item,v) in msgs">
							<div class="hear clear">
								<a style="font-size:0.3rem;">{{name}}</a>
								<span style="font-size:0.25rem;float:right">{{item.send_time}}</span>
							</div>
							<p style="font-size:0.28rem;margin-top:0.1rem;">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.send_content}}</p>
							<div class="hear clear" style="margin-top: 0.2rem;">
								<a style="font-size:0.3rem;color:#40cc8b">客服：</a>
								<span style="font-size:0.25rem;float:right">{{item.reply_time}}</span>
							</div>
							<p v-if="item.reply_conten == ''" style="font-size:0.28rem;color:#ccc;margin-top:0.1rem;">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;暂无回复</p>
							<p v-if="item.reply_conten != ''" style="font-size:0.28rem;margin-top:0.1rem;">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.reply_conten}}</p>

						</div>
					</div>
					<!-- <Page :total="140" size="small" show-total></Page> -->
				</Tab-pane>
			</Tabs>		
		</div>
					</mt-loadmore>
		
	</div>
</template>

<script>
	import kwz from '../utils/kwz.js'
	import { Toast } from 'mint-ui'
	var timer=null;
	export default {
		data(){
			return {
				flag:false,
				single:true,
				sendMsg:'',
				msgs:[],
				name:'',
				pages:1,
				countall: 0,
				pagesize: 10,
				pccounts:[],
				allLoaded:false
			}
		},
		created:function () {
			//获取留言列表
			var that=this;
			var url = kwz.project + '/getHdLyList'
			kwz.acall2({
				url : url,
				params : null,
				type : 'GET',
				success : function(data){
					if(data.StatusCode==200){
						that.msgs = data.data.T1;
						that.countall=data.data.T0[0].countall;
						//遍历短信的pccount加入pccounts数组中
						for (var i = 0; i < data.data.T1.length; i++) {
							that.pccounts.push(data.data.T1[i].pccount)
						}
					}else{
						alert("网络异常，稍后重试！");
					}
				},
			});
			//获取用户姓名
			var url_1 = kwz.project + '/getMyInfo'
			kwz.acall2({
				url : url_1,
				params : null,
				type : 'GET',
				success : function(data){
					if(data.StatusCode==200){
						that.name=data.data.uname;
					}else{
						alert("网络异常，稍后重试！");
					}
				},
			 })
		},
		watch:{
			
	
		},
		methods: {
			clearInput(){
				$(".tsxx").html("");
			},
			//发送留言
			subimt () {
				var that=this;
				//是否需要回复
				let ishf = 1;
				//判断是否需要回复
				if(this.single == true){
					ishf = 1;
				}else{
					ishf = 0;
				}
				//获取留言信息
				//发送信息
				var sendMsg=encodeURI(this.sendMsg);
				if (!sendMsg && sendMsg.trim()=='') {
					$(".tsxx").html("发送信息不能为空！")
					return;
				}
				var url = kwz.project + '/sendTeacherAssistant';
				kwz.acall2({
					url : url,
					// params : null,
					type : 'POST',
					params:{'msg':sendMsg,'ishf':ishf},
					dataType:'JSON',
					success: function(data){
						if(data.StatusCode==200){
							if(data.data.status == 'success'){
								that.sendMsg='';
								that.seccondFind();
								Toast({
									message: '发送成功',
									iconClass: 'icon icon-success',

								});
								// setTimeout(function(){
								// 	history.go(-1);
								// },3000)

							}else{
								Toast({
									message: '发送失败',
									iconClass: 'icon icon-success',

								});
							}
						}else{
							alert("网络异常，稍后重试！");
						}
					}
				})
			},
			loadBottom(){//上拉分页
			var that=this;
			if(that.pages>this.countall/this.pagesize){
				 this.allLoaded = true;// 若数据已全部获取完毕
  				 this.$refs.loadmore.onBottomLoaded();
			}	
		   	that.pages++;
		   	
		   	setTimeout(() => {
			var url= kwz.project +'/getHdLyList?page='+that.pages;
			kwz.acall2({
				url : url,
				params : null,
				type : 'GET',
				success: function(data){
					if(data.StatusCode==200){
						that.msgs=that.msgs.concat(data.data.T1);
					}else{
						alert("网络异常，稍后重试！");
					}
				}
			}) }, 2000);
            // this.$refs.loadmore.onTopLoaded();
           },
           seccondFind(){
           	var that=this;
			var url = kwz.project + '/getHdLyList'
			kwz.acall2({
				url : url,
				params : null,
				type : 'GET',
				success : function(data){
					if(data.StatusCode==200){
						that.msgs = data.data.T1;
						that.countall=data.data.T0[0].countall;
						//遍历短信的pccount加入pccounts数组中
						for (var i = 0; i < data.data.T1.length; i++) {
							that.pccounts.push(data.data.T1[i].pccount)
						}
					}else{
						alert("网络异常，稍后重试！");
					}
				},
			});
           }

		},
		
			
	}
</script>

<style scoped>
	.list{
		border-bottom: 1px solid #ccc;
		padding: 0.1rem 0;
		box-sizing: border-box;
	}
	.hear{
		width: 100%;
		height: auto;
		
		
	}
	.main{
		width: 100%;
		height: auto;
		margin-top: -.2rem;
		border-bottom: 1px solid #fff!important;
	}
	.dc{
		position: absolute;
		top: 0px;
		bottom: 0px;
		width: 100%;
		background-color: #fff;
		z-index: 0;
	}
	.content{
		font-size: 0.33rem;
		position: relative;
		/*z-index: 3;*/
		margin-top: 0.88rem;
		width: 100%;
		height: 100%;
		background-color: #fff;
		padding: 0.3rem 0.4rem;
		box-sizing: border-box;
	}
	.lylx{
		width: 100%;
		border-radius: 0.1rem;
	}
	
	textarea{
		width: 100%;
		height: 3rem;
		border: 0.01rem solid #e7e7e7;
		margin-top: 0.3rem;
		resize: none;
		border-radius: 0.1rem;
		padding: 0.2rem;
		line-height: 0.4rem;
		box-sizing: border-box;

	}
	.tj{
		margin-top: 0.3rem;
		border: 0px;
		width: 100%;
		height: 1rem;
		border-radius: 1rem;
		background-color: #0caedf;
		color: #fff;
		font-size: 0.35rem;
	}
	.tj:active{
		opacity: .5;
	}
	.clear:after{
		content: '';
		display: block;
		clear: both;
	}
	.tsxx{
		color: red;
	    margin-top: -.1rem;
	    margin-left: .5rem;
	}
	.msgNull{
		font-size: 0.32rem;
    	padding-left: 2.5rem;
		position: relative;
	    background-color: #fff;
	    width: 100%;
	    height: 20rem;
	    /* top: .88rem; */
	    padding-top: .5rem;
	    padding-left: 2rem;
	}

</style>


