<template>
	<div>
		<header>
	       <img @click="back" class="fan" src="../../gzxtkt/images/xs_pic_fh.png"/>
			<p>新建相册</p>
		</header>
		<div class="dc"></div>
		<div class="bj">
			<div class="bjmz">
				<!-- <span style="font-size:0.3rem">相册名称:</span> -->
				<input type="text" name="" value="" v-model="name" placeholder="添加相册名称">
			</div>
			<div class="bjmz" style="margin-top: 0.5rem;">
				<!-- <span style="font-size:0.3rem">相册描述:</span> -->
				<input type="text" name="" value="" v-model="msg" placeholder="添加相册描述">
			</div>
			<Button @click="submit" style="margin-top: 0.5rem;background-color: #0caedf;border: none" type="success" long>完成</Button>
		</div>

	</div>
</template>
<script>
	import kwz from '../utils/kwz.js'
	import { Toast } from 'mint-ui';
	export default {
		data() {
			return {
				name:'',
				// arrs:[],
				aid:'',
				msg:'',
			}
		},
		created:function() {
			this.aid = this.$route.query.newXc;
		},
		methods:{
			back() {
				history.go(-1)
			},
			//更新相册
			submit(){
				let gxname = this.name;
				let gxmsg=encodeURI(this.msg);
				var url_xj = kwz.project + '/addAlbum';
				var that = this;
				kwz.acall2({
					url : url_xj,
					dataType: 'json',
					type : 'POST',
					params:{'aname':gxname,'aintro':gxmsg,'claid':that.aid,},
					success : function(data){
						if(data.StatusCode==200){
							let instance=Toast({
								message: '新增成功',
								iconClass: 'icon icon-success',
								duration: 3000
							});
							setTimeout(() => {
								instance.close();
							}, 1000);

							setTimeout(function(){
								history.go(-1);
							},1000)
						}else{
							alert("网络异常，稍后重试！");
						}
					},
				});
			}
		}
	}
</script>
<style scoped>
	.dc{
		position: absolute;
		top: 0px;
		bottom: 0px;
		width: 100%;
		background-color: #fff;
		z-index: 0;
	}
	.fan{
		position: absolute;
		left: 0.25rem;
		top: 0.26rem;
		width: 0.37rem;
		height: 0.35rem;
	}
	.bj{
		width: 100%;
		height: auto;
		border-bottom: 1px solid #ccc;
		position: relative;
		top: 0.88rem;
		padding: 0.3rem;
		box-sizing: border-box;
	}
	.bjmz input{
		border: none;
		height: 0.6rem;
		font-size: 0.3rem;

	}
</style>


