<template>
	<div>
		<header>
			<router-link to='/' class="goback"></router-link>
			<p>发送OA</p>
		</header>
		<div class="dc"></div>
		<div class="content">
			<div class="lylx">
			  	<div class="xzfz">选择分组:</div>
			  	<div class="select_wz" @click="flag=true">
			  		<Select v-model="model1" size="large" style="width:100%" placeholder="全部">
			  			<Option value="">全部</Option>
					    <Option v-for="(item,v) in groupList" :key="v" :value="item.custom_id" >{{ item.custom_name }}</Option>
					</Select>
			  	</div>
				<div class="l_nj" v-show='flag'>
					<div style="display:flex;">
						<p>选择发送对象(<span style="color: red;">点击选择，再点取消</span>)</p>
						<div :class="{qx_div:!isqx}" @click="selectAll"><div :class="{qx:isqx}">全选</div></div>
					</div>
					<div class="l_n_l">
						<div class="nj_list" v-for='(item, i) in groupTeacher' :key="item.uid">
							<input type="checkbox" name="" :id='"a"+i' :value='item.uid' v-model='list'>
							<label :for='"a"+i' @click="clickOne(item,i)">
								<p :class="{listTea:item.isChecked}">{{item.username}}({{item.zw}})</p>
							</label>
						</div>
					</div>
				</div>
			</div>

			<textarea maxlength="255" v-model='message'></textarea>
			
			<p class="tips">限255字，您已输入<span style="color: red;">{{conut}}</span>字</p>
			
			<p class="title"><i>{{shi}}</i></p>

			<div style="position:relative;">
				<input id='t_begin' type="text" name="" data-lcalendar="2000-01-01,2018-01-29" readonly class="time" placeholder="请输入定时留言时间" v-init v-model='time'>
				<i class="del" @click="delet">X</i>
			</div>
			<button class="tj" @click='_submit'>发送</button>
		</div>
		


	</div>
</template>

<script>
	var timer=null;
	import { Toast } from 'mint-ui';
	import kwz from '../utils/kwz.js'
	export default {
		data(){
			return {
				flag: false,
				shi:'定时留言,请选择时间',
				// 留言信息
				message: '',
				// 选中列表
				list: [],
				// 预定时间
				time: '',
				neirong:'',
				txpc:'',
				utype:'',
				groupList: [],
				model1:'',
				groupTeacher:'',
				tids:'',
				isqx:false,
				isqx_div:true,
				isall:false,
				isOne:true,
			}
		},
		created: function(){
			this.message=kwz.getMyInfo.uname+"的留言：";
			var that=this;
			var url1 = kwz.project + '/getSchTeacherList';
				kwz.acall2({
					url : url1,
					params : null,
					type : 'GET',
					success: function(data){
						if(data.StatusCode==200){
								that.groupTeacher = data.data;
							}else{
								alert("网络异常，稍后重试！");
						}
					}
				})
			var url = kwz.project + '/getOACustomList'
			kwz.acall2({
				url : url,
				params : null,
				type : 'GET',
				success: function(data){
					if(data.StatusCode==200){
							that.groupList = data.data;
						}else{
							alert("网络异常，稍后重试！");
					}
				}
			})
			var url_5 = kwz.project + '/getMsgPc'
			$.ajax({
				url: url_5,
				dataType: 'json',
				type: 'GET',
				success: function(data){
					if(data.StatusCode==200){
						that.txpc = data.data.pc;
					}
					if (data.data.status == "fail") {
						alert(data.data.errinfo)
					}
				}
			});
		},
		watch: {
			time:function () {
				if (this.time != '') {
					this.shi = '时间选定';
					$('.title').css({color:"#40cc8b"})
				}
			},
			model1(val,oldval){
				this.isqx=false;
				this.flag=true;
				this.groupTeacher=[];
				this.indeterminate=false;
				this.checkAll=false;
				var that=this;
				var url = kwz.project + '/getSchTeacherList?cid='+val;
				kwz.acall2({
					url : url,
					params : null,
					type : 'GET',
					success: function(data){
						if(data.StatusCode==200){
								that.groupTeacher = data.data;
							}else{
								alert("网络异常，稍后重试！");
						}
					}
				})
			},
		},
		computed: {
			conut: function(){
				return this.message.length;
			},
		},
		directives: {
			init: {
				inserted: function(){
					var calendar = new lCalendar();
					calendar.init({
					    'trigger': '#t_begin',
					    'type': 'datetime'
					});
				}
			},
			
		},
		methods: {
			selectAll(){
				if(this.isqx){
					this.isqx=false;
					this.isall=false;
					this.list=[];
					// $(".nj_list [input]").checked=true;
					this.groupTeacher.forEach(function(item){
						item.isChecked = false
					})

				}else{
					this.isqx=true;
					this.isall=true;
					for (var i = 0; i < this.groupTeacher.length; i++) {
						this.list.push(this.groupTeacher[i].uid)
					}
					// this.list=this.groupTeacher;
					this.groupTeacher.forEach(function(item){
						item.isChecked = true
					})
				}
			},
			//清空时间
			delet () {
				if (this.time != '') {
					this.time = '';
					this.shi = '定时留言,请选择时间';
					$('.title').css({color:"#000"})
				}else{
					
				}
			},
			_submit: function(){
				kwz.getOutboxList='';
				var self=this;
				// 选中列表成员id
				var ids='';
				// 是否有预定时间
				var isds=0;
				var time='';
				//发送内容
				if(this.time!=''){
					isds=1;
					time=this.time+":00";
				}
				for(let i=0;i<this.list.length;i++){
					if(i!=this.list.length-1){
						ids+=this.list[i]+',';
					}else{
						ids+=this.list[i];
					}
				}

					// 提交接口地址
					// debugger
					var url='';
					var sendMsg=encodeURI(self.message);
					var par={'msgtype':75,'isds':isds,'sendtime':time,'tealist':ids,'msg':sendMsg,'pc':self.txpc};
					
					if (self.neirong == sendMsg) {
						Toast('信息内容不能为空')
					}else{
						var url_1 = kwz.project + '/insertMsg'
					kwz.acall2({
						url:url_1,
						dataType: 'json',
						type: 'POST',
						params:par,
						success: function(data){
							if (data.StatusCode==200) {
								if(data.data.status == "success"){
										Toast({
											message: '发送成功',
											iconClass: 'icon icon-success',

										});
										setTimeout(function(){
											history.go(-1);
										},3000)	
								}else{
									alert(data.data.errinfo)
								}	
								
							}						
						},
						
					});
					}
			},
			clickOne(item,i){
				// debugger
				if(this.isall){
					if(item.isChecked == undefined || item.isChecked){
						// item.isChecked = false
						this.$set(this.groupTeacher[i],'isChecked',false)
					}else{
						// item.isChecked = true
						this.$set(this.groupTeacher[i],'isChecked',true)
					}	
				}else{
					if(item.isChecked!=undefined && item.isChecked){
						this.$set(this.groupTeacher[i],'isChecked',false)
					}else{
						this.$set(this.groupTeacher[i],'isChecked',true)
					}	
				}
				var that = this
				setTimeout(function(){	
					if(that.list.length==that.groupTeacher.length){
						// debugger
						that.isqx=true;
					}else{
						that.isqx=false;
					}		
				},100)
				

			}
		}
	}
</script>

<style scoped>
	.del{
		position:absolute;right:0;top:0.20rem;
		display: inline-block;
		width: 0.6rem;
		height: 0.6rem;
		/* background-color: orange; */
		text-align: center;
		line-height:0.60rem;
		font-size: 0.33rem;
		color: red;
		border-radius: 0.08rem;
	}
	.del:active{
		background-color: #40cc8b;
		color: white;
	}
	/* 弹窗 */
	/* .tan{

	}
 */
	.dc{
		position: absolute;
		top: 0px;
		bottom: 0px;
		width: 100%;
		background-color: #fff;
		z-index: 2;
	}
	.content{
		position: relative;
		z-index: 3;
		margin-top: 0.88rem;
		width: 100%;
		background-color: #fff;
		padding: 0.3rem;
	}
	.lylx{
		width: 100%;
		border: 0.01rem solid #e7e7e7;
		border-radius: 0.1rem;
	}
	.l_s,.l_g{
		display: flex;
		flex-flow: row;
		padding: 0.2rem;
	}
	.l_s p,.l_g p{
		flex: 1;
		font-size: 0.35rem;
		color: #111111;
		line-height: 0.8rem;
	}
	.l_s select,.l_g select{
		flex: 2;
		background-color: #fff;
		width: 4.6rem;
		border-radius: 0.1rem;
		font-size: 0.35rem;
		padding-left: 0.2rem;
		background-image: url('../../gzxtkt/images/ico_17.png');
		background-size: 0.3rem 0.1rem;
		background-repeat: no-repeat;
		background-position: 4rem center;
	}
	.l_nj{
		padding: 0.2rem;
		width: 100%;
		border-top: 0.01rem solid #e7e7e7;
	}
	.l_nj p{
		font-size: 0.3rem;
		color: #222222;
	}
	textarea{
		width: 100%;
		height: 3rem;
		border: 0.01rem solid #e7e7e7;
		margin-top: 0.3rem;
		resize: none;
		border-radius: 0.1rem;
		padding: 0.2rem;
		font-size: 0.33rem;
		line-height: 0.4rem;
	}
	.tips{
		float: right;
		font-size: 0.33rem;
		margin-top: 0.1rem;
		color: #111111;
	}
	.title{
		clear: both;
		font-size: 0.3rem;
		color: #222222;
		margin-top: 0.8rem;
		line-height: 0.45rem;
	}
	.title:before{
		content: '';
		width: 0.1rem;
		border-left: 0.1rem solid #1bb7ff;
		height: 0.45rem;
		margin-right: 0.2rem;
	}
	.time{
		width: 100%;
		height: 1rem;
		border: 0px;
		border-bottom: 0.01rem solid #e7e7e7;
		padding-left: 0.2rem;
		font-size: 0.33rem;
	}
	.tj{
		margin-top: 0.3rem;
		border: 0px;
		width: 100%;
		height: 1rem;
		border-radius: 1rem;
		background-color: #0cacde;
		color: #fff;
		font-size: 0.35rem;
	}

	/*加载列表*/
	.l_n_l{
		width: 100%;
		display: flex;
		flex-wrap: wrap;
	}
	.nj_list{
		border: 0.01rem solid #1bb7ff;
		border-radius: 0.1rem;
		background-color: #fff;
		transition: background-color .5s;
		margin-right: 0.2rem;
		margin-top: 0.2rem;
	}
	.nj_list input{
		display: none;
	}
	.nj_list p{
		font-size: 0.3rem;
		padding: 0.2rem 0.2rem;
		color: #1bb7ff;
		transition: color .5s;
	}

	/*个人留言*/
	.l_g{
		border-top: 0.01rem solid #e7e7e7;
	}
	.tj:active{
		opacity: .5;
	}
	 .select_div{
   	    border: 1px solid #e7e7e7;
	    width: 90%;
	    margin-left: 5%;
	    height: 1.5rem;
	    border-radius: 5px;
	    margin-top: .4rem;
	    display: flex;
    }
    .select_wz{
    	 position: relative;
	    left: 2.3rem;
	    top: -0.2rem;
	    width: 60%;
    }
    .xzfz{
		font-size: .32rem;
	    position: relative;
	    top: .4rem;
	    font-weight: bold;
	    margin-left: .5rem;
    }
    .qx{
    	background-color: #0cacde;
	    margin-left: .8rem;
	    color: #fff;
	    width: .8rem;
	    text-align: center;
	    padding-top: .04rem;
	    border-radius: 5px;
	    z-index: 99999999;
    }
    .qx_div{
    	/*background-color: #0cacde;*/
	    margin-left: .8rem;
	    color:  #0cacde;
	    width: .8rem;
	    text-align: center;
	    padding-top: .04rem;
	    border-radius: 5px;
	    border: 1px solid  #0cacde;
    }
    .listTea{
    	background-color: #1bb7ff;
    	color: #fff!important;
    }
    .teacher_name{
    	font-size: .32rem;
	    color: #0cacde;
	    padding: .1rem;
    }
   
</style>


