<template>
	<div>
		<header>
	       <img @click="back" class="fan" src="../../gzxtkt/images/xs_pic_fh.png"/>
			<p>编辑相册</p>
		</header>
		<div class="dc"></div>
		<div class="bj">
			<div class="bjmz">
				<span style="font-size:0.3rem">相册名称:</span>
				<input type="text" name="" value="" v-model="albumDetails.aname">
			</div>
			<div class="bjmz" style="margin-top: 0.5rem;">
				<span style="font-size:0.3rem">相册描述:</span>
				<input type="text" name="" value="" v-model="albumDetails.aintro">
			</div>
			<Button @click="submit" style="margin-top: 0.5rem;background-color: #0caedf;border: none" type="success" long>完成</Button>
		</div>

	</div>
</template>
<script>
	import kwz from '../utils/kwz.js'
	export default {
		data() {
			return {
				name:'',
				arrs:[],
				aid:'',
				msg:'',
				albumDetails:[]
			}
		},
		created:function() {
			this.arrs=(this.$route.query.name).split(",");
			for (var i = 0; i < this.arrs.length; i++) {
				this.name = this.arrs[0];
				this.aid = this.arrs[1];
			};
			var that=this;
				var url=kwz.project+'/getAlbumDetails?aid='+this.aid;
				kwz.acall2({
					url:url,
					params:null,
					type:'GET',
					success:function(res){
						if (res.data.status=="fail") {
							alert(res.data.errinfo)
						}else{
							that.albumDetails=res.data[0];
						}	
					}
				});
			// this.name = this.$route.query.name;
		},
		methods:{
			back() {
				history.go(-2)
			},
			//更新相册
			submit(){
				let gxname = encodeURI(this.albumDetails.aname);
				let gxmsg=encodeURI(this.albumDetails.aintro);
				var url_gx = kwz.project + '/editAlbum?aid='+this.aid+'&aname='+gxname+'&aintro='+gxmsg;
				var that = this;
				kwz.acall2({
					url : url_gx,
					type : 'POST',
					params:null,
					success : function(data){
						if(data.StatusCode==200){
							history.go(-2);
						}else{
							alert("网络异常，稍后重试！");
						}
					},
				});
			}
		}
	}
</script>
<style scoped>
	.dc{
		position: absolute;
		top: 0px;
		bottom: 0px;
		width: 100%;
		background-color: #fff;
		z-index: 0;
	}
	.fan{
		position: absolute;
		left: 0.25rem;
		top: 0.26rem;
		width: 0.37rem;
		height: 0.35rem;
	}
	.bj{
		width: 100%;
		height: auto;
		border-bottom: 1px solid #ccc;
		position: relative;
		top: 0.88rem;
		padding: 0.3rem;
		box-sizing: border-box;
	}
	.bjmz input{
		border: none;
		height: 0.6rem;
		font-size: 0.3rem;

	}
</style>


