<template>
	<div>
		<header v-if="h_flag">
	       <router-link to='/index' class="goback"></router-link>
			<p>通讯录</p>
			<div class="find_icon" @click="h_flag=false"><Icon type="search"></Icon></div>
		</header>
		<div v-if="!h_flag" class="find_msg">
				<div class="header_icon">&nbsp;<Icon type="search"></Icon></div>
				<div><input v-model="findContent" type="text" name="" placeholder=" 搜索用户名"></div>
				<button class="find_qx" @click="find_quxiao">取消</button>
		</div>
			<div class="teacher_child">
				<div :class="{ active: isStu }" @click="changeScolor">学生</div>
				<div :class="{ active: isTea }" @click="changeTcolor">老师</div>
			</div>
			<div class="ssjg" v-if="flag_show">
				<div class="icon_show"><Icon type="information-circled"></Icon></div>
				<div>暂无相关搜索结果</div>
			</div>
			<div class="find_stu" v-if="find_flag">
				<template>
					   <Select v-model="model1"  size="large" style="width:50%" placeholder="全部">
					       <Option value="">全部</Option>
					        <Option v-for="(item,v) in gradeList" :value='item.graid' :key="v">{{item.graname}}</Option>
					   </Select>
				</template>
				<template>
					   <Select v-model="model2"  size="large" style="width:50%" placeholder="全部">
					       <Option value="">全部</Option>
					       <Option v-for="(item,v) in classList" :value='item.claid' :key="v">{{item.claname}}</Option>
					   </Select>
				</template>
			</div>
			<div class="line"></div>
		<div v-if="flag" >
			<div class="main">
				<div class="tea_list">
					<div v-for="item in teachers">
						<div class="tea">
							<div>{{item.uname}}</div>
							<div><a :href="'tel:'+item.mobile">{{item.name}}&nbsp;&nbsp;<Icon type="android-call"></Icon></a></div>
						</div>
						<div class="line_tea"></div>
					</div>
				</div>
			</div>
		</div>
		<div v-else>
		<div class="main">
			<div>
				<div class="stu_list">
					<div>
					<div v-for="item in students">
						<div class="tea">
							<div>{{item.uname}}</div>
							<div><a :href="'tel:'+item.mobile">{{item.bjname}}&nbsp;&nbsp;<Icon type="android-call"></Icon></a></div>
						</div>
						<div class="line_tea"></div>
					</div>
					</div>
				</div>
			</div>
		</div>
		</div>
		<div v-if="loading" class="load">
			<div class="loading">
		        <span></span>
		        <span></span>
		        <span></span>
		        <span></span>
		        <span></span>
			</div>
		</div>
	</div>
</template>
<script>
	import kwz from '../utils/kwz.js'
	import { Loadmore } from 'mint-ui';
	export default {
		data() {
			return {
				isStu:false,
				isTea:true,
				flag:true,
                model1: '',
                model2: '',
                h_flag:true,
                findContent:'',
                page: 1,
                paget:1,
				countall_stu: 0,
				countall_tea: 0,
				countall_res:10,
				pagesize: 10,
				allLoaded:false,
				allLoaded_1:false,
				students:[],
				teachers:[],
				gradeList:[],
				classList:[],
				find_flag:false,
				loading:false,
				val1:'',
				val2:'',
				page1:1,
				page2:1,
				flag_show:false,
			}
		},
		 watch: {
		 	findContent:function(val,oldval){
		 		var that=this;
		 		if(this.isStu){
		 			var vals=encodeURI(val);
		            var url=kwz.project+'/getTxlStudentList?uname='+vals;
		            kwz.acall2({
		                url:url,
		                params:null,
		                type:'GET',
		                success:function(res){
		                    if (res.StatusCode==200) {
		                         that.students=res.data.T1;;
		                         if(that.students==''){
		                         	that.flag_show=true;
		                         }else{
		                         	that.flag_show=false;
		                         }
		                    }  
		                }
		            });
		 		}else{
		 			var vals=encodeURI(val);
		 			var url_tea=kwz.project+'/getTxlTeacherList?uname='+vals;
		            kwz.acall2({
		                url:url_tea,
		                params:null,
		                type:'GET',
		                success:function(res){
		                    if (res.StatusCode==200) {
		                         that.teachers=res.data.T1;
		                         if(that.teachers==''){
		                         	that.flag_show=true;
		                         }else{
		                         	that.flag_show=false;
		                         }
		                    }  
		                }
		            });
		 		}
		 	},
		 	model1(val,oldval){
		 		document.getElementsByTagName('body')[0].scrollTop = 0;//滚动条置顶
		 		this.page1=1;
		 		this.val1=val;
		 		if(val==''){
		 			this.model2='';
		 		}
		 		var that=this;
		 		var url_cla=kwz.project+'/getClassList?graid='+val;
	            kwz.acall2({
	                url:url_cla,
	                params:null,
	                type:'GET',
	                success:function(res){
	                    if (res.StatusCode==200) {
	                         that.classList=res.data;
	                    }  
	                }
	            });
	            var url_stu1=kwz.project+'/getTxlStudentList?njid='+val;
	            kwz.acall2({
	                url:url_stu1,
	                params:null,
	                type:'GET',
	                success:function(res){
	                    if (res.StatusCode==200) {
	                         that.students=res.data.T1;
	                    }  
	                }
	            });
		 	},
		 	model2(val,oldval){
		 		document.getElementsByTagName('body')[0].scrollTop = 0;//滚动条置顶
		 		this.page2=1;
		 		this.val2=val;
		 		var that=this;
		 		var url_stu1=kwz.project+'/getTxlStudentList?bjid='+val;
	            kwz.acall2({
	                url:url_stu1,
	                params:null,
	                type:'GET',
	                success:function(res){
	                    if (res.StatusCode==200) {
	                         that.students=res.data.T1;
	                    }  
	                }
	            });
		 	}
		 },
		created:function() {
			document.getElementsByTagName('body')[0].scrollTop = 0;//滚动条置顶
			// 上拉加载
			var that=this;
			window.addEventListener('scroll', this.eventlistener,false)
			if(!kwz.getTxlStudentList){
	            var url=kwz.project+'/getTxlStudentList';
	            kwz.acall2({
	                url:url,
	                params:null,
	                type:'GET',
	                success:function(res){
	                    if (res.StatusCode==200) {
	                         that.students=res.data.T1;
	                         that.countall_stu=res.data.T0[0].countall;
	                         kwz.getTxlStudentList=res.data;
	                    }  
	                }
	            });
			}else{
				 that.students=kwz.getTxlStudentList.T1;
	             that.countall_stu=kwz.getTxlStudentList.T0[0].countall;
			}
			if(!kwz.getTxlTeacherList){
	            var url_tea=kwz.project+'/getTxlTeacherList';
	            kwz.acall2({
	                url:url_tea,
	                params:null,
	                type:'GET',
	                success:function(res){
	                    if (res.StatusCode==200) {
	                         that.teachers=res.data.T1;
	                         that.countall_tea=res.data.T0[0].countall;
	                         kwz.getTxlTeacherList=res.data;
	                    }  
	                }
	            });
			}else{
				that.teachers=kwz.getTxlTeacherList.T1;
	            that.countall_tea=kwz.getTxlTeacherList.T0[0].countall;
			}
			if(!kwz.getGradeList){
	            var url_gra=kwz.project+'/getGradeList';
	            kwz.acall2({
	                url:url_gra,
	                params:null,
	                type:'GET',
	                success:function(res){
	                    if (res.StatusCode==200) {
	                         that.gradeList=res.data;
	                         kwz.getGradeList=res.data;
	                    }  
	                }
	            });
			}else{
				that.gradeList=kwz.getGradeList;
			}
		},
		methods:{
			eventlistener:function(){
				var that=this;
				if(document.body.scrollTop+document.documentElement.clientHeight==document.body.scrollHeight){
					if(that.$route.path=='/txl'){
						if(that.flag){
							that.loadBottom_teacher();	
						}else{
							if(that.val1==''){
								that.loadBottom_student();
							}else{
								if(that.val2==''){	
									that.load_student(that.val1,1);
								}else{
									that.load_student(that.val2,2);
								}
							}
						}
					}
				}
			},
			load_student(val,num){
				if(num=="1"){
					if(this.page1>this.countall_res/this.pagesize){
						this.info();
						this.loading=false;
						return;
					}else{	
						this.loading=true;
					}
					this.page1+=1;
					var url=kwz.project+'/getTxlStudentList?njid='+val+'&page='+this.page1;
				}else{
					if(this.page2>this.countall_res/this.pagesize){
						this.info();
						this.loading=false;
						return;
					}else{	
						this.loading=true;
					}
					this.page2+=1;
					var url=kwz.project+'/getTxlStudentList?bjid='+val+'&page='+this.page2;
				}
				var that=this;
				setTimeout(() => {
					kwz.acall2({
						url : url,
						params : null,
						type : 'GET',
						success: function(data){
							if(data.StatusCode==200){
								that.countall_res=data.data.T0[0].countall;
								that.students=that.students.concat(data.data.T1);
								that.loading=false;
							}else{
								alert("网络异常，稍后重试！");
							}
						}
				}) }, 1000);

			},
			info () {
                this.$Message.info('已无更多数据！');
            },
			find_quxiao(){
				document.getElementsByTagName('body')[0].scrollTop = 0;//滚动条置顶
				this.flag_show=false;
				this.page=1;
				this.paget=1;
				this.page1=1;
				this.page2=1;
				this.h_flag=true;
				var that=this;
				var url_tea=kwz.project+'/getTxlTeacherList';
	            kwz.acall2({
	                url:url_tea,
	                params:null,
	                type:'GET',
	                success:function(res){
	                    if (res.StatusCode==200) {
	                         that.teachers=res.data.T1;
	                         that.countall_tea=res.data.T0[0].countall;
	                    }  
	                }
	            });
	            var url=kwz.project+'/getTxlStudentList';
	            kwz.acall2({
	                url:url,
	                params:null,
	                type:'GET',
	                success:function(res){
	                    if (res.StatusCode==200) {
	                         that.students=res.data.T1;
	                         that.countall_stu=res.data.T0[0].countall;
	                    }  
	                }
	            });
			},
			changeScolor(){
				document.getElementsByTagName('body')[0].scrollTop = 0;//滚动条置顶
				this.flag_show=false;
				this.model1='';
				this.model2='';
				if(!this.isStu){
					this.isTea=false;
					this.isStu=true;
					this.flag=false;
				}
				this.find_flag=true;
				this.page=1;
				var that=this;
	            var url=kwz.project+'/getTxlStudentList';
	            kwz.acall2({
	                url:url,
	                params:null,
	                type:'GET',
	                success:function(res){
	                    if (res.StatusCode==200) {
	                         that.students=res.data.T1;
	                         that.countall_stu=res.data.T0[0].countall;
	                    }  
	                }
	            });
			},
			changeTcolor(){
				document.getElementsByTagName('body')[0].scrollTop = 0;//滚动条置顶
				this.flag_show=false;
				this.allLoaded_1= false;
				this.paget=1;
				this.find_flag=false;
				if(!this.isTea){
					this.isStu=false;
					this.isTea=true;
					this.flag=true;
				}
				var that=this;
				var url_tea=kwz.project+'/getTxlTeacherList';
	            kwz.acall2({
	                url:url_tea,
	                params:null,
	                type:'GET',
	                success:function(res){
	                    if (res.StatusCode==200) {
	                         that.teachers=res.data.T1;
	                         that.countall_tea=res.data.T0[0].countall;
	                    }  
	                }
	            });
			},
			loadBottom_teacher(){
				if(this.paget>this.countall_tea/this.pagesize){
					this.info();
					this.loading=false;
					return;
				}else{	
					this.loading=true;
				}
			   	this.paget++;
			   	var that=this;
			   	setTimeout(() => {
				var url= kwz.project +'/getTxlTeacherList?page='+this.paget;
				kwz.acall2({
					url : url,
					params : null,
					type : 'GET',
					success: function(data){
						if(data.StatusCode==200){
							that.teachers=that.teachers.concat(data.data.T1);
							that.loading=false;
						}else{
							alert("网络异常，稍后重试！");
						}
					}
				}) }, 1000);
			},
			loadBottom_student(){
				if(this.page>this.countall_stu/this.pagesize){
					this.info();
					this.loading=false;
					return;
				}else{	
					this.loading=true;
				}
			   	this.page++;
			   	var that=this;
			   	setTimeout(() => {
				var url= kwz.project +'/getTxlStudentList?page='+this.page;
				kwz.acall2({
					url : url,
					params : null,
					type : 'GET',
					success: function(data){
						if(data.StatusCode==200){
							that.students=that.students.concat(data.data.T1);
							that.loading=false;
						}else{
							alert("网络异常，稍后重试！");
						}
					}
				}) }, 1000);
			},
		},
		beforeDestroy:function(){//退出页面时注销掉下拉监听
 			window.removeEventListener("scroll",this.eventlistener,false);

		}
	}
</script>
<style scoped>
	.main{
		margin-top: .88rem;
		position: relative;
		overflow-x: hidden;
		width: 100%;
		height: 100%;
		background-color: #fff;
		/*position: fixed;*/
	}
	.teacher_child{
		width: 100%;
		height: 1.2rem;
		display: flex;
	    /*margin-left: 30%;*/
	    /*margin-top: -.12rem;*/
    	margin-bottom: .2rem;
    	position: fixed;
    	background-color: #fff;
		z-index: 9;
		border-bottom: 1px solid #e7e7e7;
	}
	.teacher_child div:nth-child(1){
		margin-top: .2rem;
		margin-left: 30%;
		border: 1px solid #0caedf;
	    width: 1.5rem;
	    text-align: center;
	    height: .7rem;
        padding: .1rem;
   		border-bottom-left-radius: 4px;
   		border-top-left-radius: 4px;
   		font-size: .32rem;
	}
	.teacher_child div:nth-child(2){
		margin-top: .2rem;
		/*margin-left: 30%;*/
		border: 1px solid #0caedf;
	    width: 1.5rem;
	    text-align: center;
	    height: .7rem;
    	padding: .1rem;
   		border-bottom-right-radius: 4px;
   		border-top-right-radius: 4px;
   		font-size: .32rem;
	}
	.active{
		background-color: #0caedf;
		color: #fff;
	}
	.tea{
		overflow: auto;
	}
	.tea div:nth-child(1){
		font-size: .32rem;
		margin-bottom: .3rem;
    	margin-top: .3rem;
    	float: left;
    	margin-left: 5%;
	}
	.tea div:nth-child(2){
		font-size: .28rem;
	    color: #999;
	    margin-top: .3rem;
	    float: right;
	    margin-right: 5%;
	}
	.line{
		border-top: 1px solid #e7e7e7;
		position: fixed;
		/*padding: .2rem;*/
	}
	.line_tea{
		border-top: 1px solid #e7e7e7;
		width: 90%;
		margin-left: 5%;
		/*padding: .2rem;*/
	}
	.tea_list{
		position: relative;
		margin-top: 1.2rem;
		margin-bottom: .3rem;
	}
	.stu_list{
		position: relative;
		margin-top: 1.8rem;
		/*margin-bottom: .6rem;*/
	}
	.find_stu{
		/*background-color: #0caedf; */
	    position: fixed;
	    width: 100%;
	    color: #fff;
	    display: -webkit-box;
	    display: -ms-flexbox;
	    display: flex;
	    margin-top: 1rem;
	    z-index: 9;
	}
	.find_icon{
		position: absolute;
	    margin-top: -.8rem;
	    /* width: 1rem; */
	    /* height: 1rem; */
	    font-size: .5rem;
	    color: #fff;
	    margin-left: 7rem;
	}
	.find_msg{
	    display: flex;
	    margin-top: -.9rem;
	    position: fixed;
	    z-index: 9;
	    background-color: #fff;
	    width: 100%;
	    height: .9rem;
	}
	.find_msg div input{
		font-size: .32rem;
	    width: 6rem;
	    height: .7rem;
	    margin-top: .1rem;;
	    background-color: #e7e7e7;
	    border: none;
	    border-radius: 0;
	    border-bottom: 1px solid #ccc;
    	border-top: 1px solid #ccc;
	}
	.header_icon{
		border-radius: 0;
		font-size: .4rem;
	    background-color: #e7e7e7;
	    height: .7rem;
	    margin-top: .1rem;
	    padding-top: .05rem;
	    margin-left: .3rem;
	    border-bottom-left-radius: 5px;
    	border-top-left-radius: 5px;
    	border-bottom: 1px solid #ccc;
    	border-top: 1px solid #ccc;
    	border-left: 1px solid #ccc;
	}
	.find_qx{
		font-size: .3rem;
	    color: #0caedf;
	    background-color: #fff;
	    height: .7rem;
	    margin-top: .1rem;
	    padding-top: .1rem;
	    border: 1px solid #ccc;
	   /* border-bottom-right-radius: 5px;
    	border-top-right-radius: 5px;*/
	}
	.student_list{
		margin-top: .1rem;
	}
	.load{
		position: fixed;
	    top: 3rem;
	    left: 3.1rem;
	}
	.loading{
        width: 80px;
        height: 40px;
        margin: 0 auto;
        margin-top:100px;
    }
    .loading span{
        display: inline-block;
        width: 10px;
        height: 100%;
        border-radius: 4px;
        background: lightgreen;
        -webkit-animation: load 1s ease infinite;
    }
    @-webkit-keyframes load{
        0%,100%{
            height: 40px;
            background: lightgreen;
        }
        50%{
            height: 70px;
            margin: -15px 0;
            background: lightblue;
        }
    }
    .loading span:nth-child(2){
        -webkit-animation-delay:0.2s;
    }
    .loading span:nth-child(3){
        -webkit-animation-delay:0.4s;
    }
    .loading span:nth-child(4){
        -webkit-animation-delay:0.6s;
        }
    .loading span:nth-child(5){
        -webkit-animation-delay:0.8s;
    }
    .ssjg{
		display: flex;
	    color: red;
	    font-size: .3rem;
	    /* padding-top: .2rem; */
	    margin-top: .2rem;
	    /* margin-bottom: -.5rem; */
	    /* margin-left: 33%; */
	    z-index: 9;
	    position: absolute;
	    top: 1.8rem;
	    background-color: #fff;
	    width: 100%;
	    height: 100%;
	    padding-left: 32%;
	    padding-top: .8rem;

	}

</style>


