<template>
	<div>
		<header>
	       <router-link to='/main' class="goback"></router-link>
			<p>多帐号选择</p>
		</header>
		<div class="main">
			<div class="tsxx"><p>您绑定以下同学课堂帐号<br>请选择帐号登录</p></div>
			<div class="tx" v-if="teachers.length>0">
				<img src="../../gzxtkt/images/laoshi.png">
				<p>老师</p>
			</div>
			<div class="xgmm" v-for="(item,v) in teachers" @click="szjs_ls(v)">
				<div class="xxmc">
					<p>{{item.sys}}</p>
					<div>{{item.schname}}</div>
				</div>
				<div class="icon_r"><Icon type="chevron-right"></Icon></div>
				<div class="number">{{item.username}}</div>
			</div>
			<div class="tx" v-if="parents.length>0">
				<img src="../../gzxtkt/images/jiazhang.png">
				<p>学生</p>
			</div>
			<div class="xgmm" v-for="(item,v) in parents" @click="szjs_jz(v)">
				<div class="xxmc">
					<p>{{item.stuname}}</p>
					<div>{{item.schname}}{{item.claname}}</div>
				</div>
				<div class="icon_r"><Icon type="chevron-right"></Icon></div>
				<div class="number">{{item.username}}</div>
			</div>
		</div>
	</div>
</template>
<script>
	import kwz from '../utils/kwz.js'
	export default {
		data() {
			return {
				teachers:[],
				parents:[],
				utype:''
			}
		},
		created:function() {
			// this.imgurl= (kwz.project+"/").replace("api/wx/","");
			var that=this;
            var url=kwz.project+'/getRoleList';
            kwz.acall2({
                url:url,
                params:null,
                type:'GET',
                success:function(res){
                    if (res.StatusCode==200) {
                         that.teachers=res.data.teacher;
                         that.parents=res.data.parent;
                    }  
                }
            });
		},
		methods:{
			szjs_jz(v){
				var url = kwz.project + '/setRole?utype=3&uid='+this.parents[v].id;
				kwz.acall2({
					url : url,
					params : null,
					type : 'GET',
					success : function(data){
						if(data.StatusCode==200){
							window.location.href="http://trtxkt.qgjydd.com/jxt/gzxtkt/wx/index.html"
							alert(data.data.errinfo)
						}else{
							alert("网络异常，稍后重试！");
						}
					},
				 })
			},
			szjs_ls(v){
				var url = kwz.project + '/setRole?utype=1&uid='+this.teachers[v].id;
				kwz.acall2({
					url : url,
					params : null,
					type : 'GET',
					success : function(data){
						if(data.StatusCode==200){
							window.location.href="http://trtxkt.qgjydd.com/jxt/gzxtkt/wx/index.html"
							alert(data.data.errinfo)
						}else{
							alert("网络异常，稍后重试！");
						}
					},
				 })
			},
		}
	}
</script>
<style scoped>
	.main{
		margin-top: 1rem;
		position: relative;
		overflow-x: hidden;
	}
	.xgmm{
	    color: #657180;
	    height: 1.2rem;
	    border-bottom: 2px solid #e7e7e7;
	    background-color: #fff;
	}
	.tx img{
	    width: 1.5rem;
	    height: 1rem;
	    /*border-radius: 100%;*/
	    margin-left: .3rem;
	    margin-top: -.2rem;
	}
	.tx{
		color: #657180;
		display: flex;
		border-bottom: 2px solid #eee;
		height: 1rem;
    	margin-top: .3rem;
	}
	.tx p{
		font-size: .32rem;
	    /*font-weight: bold;*/
	    padding: .3rem;
	    line-height: .2rem;
	}
	.xxmc{
		 display: flex;
	}
	.tsxx{
	    background-color: #e7e7e7;
	    text-align: center;
	    height: 2rem;
	    position: relative;
	}
	.tsxx p{
		color: #999;
	    font-size: .32rem;
	    position: absolute;
	    margin-top: .5rem;
	    /* text-align: center; */
	    margin-left: 25%;

	}
	.xxmc p{
		font-size: .32rem;
	    font-weight: bold;
	    margin-left: .3rem;
	    margin-top: .1rem;
	}
	.xxmc div{
	    font-size: .28rem;
	    margin-left: .5rem;
	    margin-top: .1rem;
	    color: #999;
	}
	.icon_r{
		position: relative;
    	right: -7rem;
    	top: -.3rem;
	}
	.number{
	    color: #999;
	    margin-left: .3rem;
	    margin-top: -.3rem;
	}
	.line_css{
		position: absolute;
	    width: 100%;
	    height: .3rem;
	    background-color: #e7e7e7;
	    margin-top: -.36rem;
	}
</style>


