<template>
	<div>
	    <header>
	        <router-link to='/' class="goback"></router-link>
			<p>已收OA</p>
		</header>
	<!-- <div class="dc"></div> -->
		<div class="main" v-if="lengths==0">
		<span class="msgNull"><Icon style="color: #39f" type="information-circled"></Icon>&nbsp;&nbsp;&nbsp;暂无已收OA短信</span>
		</div>
		<div v-else>
			<div class="sentCont">
			<mt-loadmore :bottomDistance=70 :bottomLoadingText="null" :bottomDropText="null" :bottomPullText="null" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" >
				<div class="sentMain">
					<!-- 信息内容 -->
					<div class="sentList" v-for="(item,v) in msgs" @click="updata(v)">
						<div class="sentListName">
							<div style="position:relative">
								<i v-if="item.flag==0" class="dian"></i>
								<img src="../../gzxtkt/images/login-icon06.png" alt="图片" style="width:0.4rem;height:0.4rem;line-height:0.6rem;"></div>
								<div style="margin-left:0.15rem">
									<span style="font-size:0.28rem;"><a style="color: #0caedf">{{item.msgtypes}}</a>&nbsp;{{item.sendname}}</span>
								</div>
								<div style="color:#ccc" class="time">{{item.recvtime}}</div>

							</div>
							<div class="sentListCont" v-if="item.flag==1">
								<p style="line-height:0.4rem;">{{item.msg}}</p>
							</div>
							<div class="sentListCont" v-else>
								<p style="line-height:0.4rem;color: #39f;">{{item.msg}}</p>
							</div>

						</div>
					</div>
			</mt-loadmore>
				</div>
			</div>
		</div>

	</div>
</template>

<script>
	import kwz from '../utils/kwz.js'
	export default {
		data(){
			return {
				msgs: [],
				page: 1,
				countall: 0,
				pagesize: 10,
				allLoaded:false,
				lengths:0
			}
		},
		created: function(){
			var that=this;
			var url = kwz.project + '/getRecvSchMsg'
			kwz.acall2({
				url : url,
				params : null,
				type : 'GET',
				success : function(data){
					if(data.StatusCode==200){
						that.msgs=data.data.T1;
						that.countall=data.data.T0[0].countall;
						that.lengths = data.data.T1.length;
					}else{
						alert("网络异常，稍后重试！");
					}
				},
			 })
		},
		methods: {
		loadBottom(){//上拉分页
			if(this.page>this.countall/this.pagesize){
				 this.allLoaded = true;// 若数据已全部获取完毕
  				 this.$refs.loadmore.onBottomLoaded();
			}	
		   	this.page++;
		   	var that=this;
		   	setTimeout(() => {
			var url= kwz.project +'/getRecvSchMsg?page='+this.page;
			kwz.acall2({
				url : url,
				params : null,
				type : 'GET',
				success: function(data){
					if(data.StatusCode==200){

						that.msgs=that.msgs.concat(data.data.T1);
					}else{
						alert("网络异常，稍后重试！");
					}
				}
			}) }, 2000);
            // this.$refs.loadmore.onTopLoaded();
           },
           // 更新状态
           updata(v){

           	$('.sentList').eq(v).find("i").css({display:"none"})
           	if($(".sentListCont p").eq(v).css({color:"#657180"})){
           	}else{
           		$(".sentListCont p").eq(v).css({color:"#000"});
           	}

           	var that=this;
           	var url = kwz.project + '/editMsgStatus?msgid='+that.msgs[v].msgid
           	kwz.acall2({
           		url : url,
           		params : null,
           		type : 'GET',
           		success : function(data){
           			if(data.StatusCode==200){
           				// that.msgs=data.data.T1;
						// that.countall=data.data.T0[0].countall;
					}else{
						alert("网络异常，稍后重试！");
					}
				},
			})
           }
		}
	}
</script>

<style scoped>
	.dian{
		display: inline-block;
   	 	width: 0.10rem;
    	height: 0.10rem;
    	background-color: red;
    	border-radius: 100%;
    	position:absolute;

	}
	.dc{
		position: absolute;
		top: 0px;
		bottom: 0px;
		width: 100%;
		background-color: #fff;
		z-index: 0;
	}
	.sentCont{
		width: 100%;
		height: auto;
		background-color: white;
		padding: 0.2rem 0.3rem;
		box-sizing: border-box;
		position: relative;
    	top: 0.88rem;
	}
	.sentMain{
		width: 100%;
		height: auto;
	}
	.sentList{
		width: 100%;
		height: auto;
		border-bottom: 1px solid #e3e3e3;
		margin-bottom: 0.15rem;
	}
	.sentListName{
		width: 100%;
		height: auto;
		/* background-color: #b4c029; */
		display: flex; 
	}
	.sentListCont{
		width: 100%;
		height: auto;
		padding: 0.2rem;
		box-sizing: border-box;
		font-size: 0.31rem;
		/* background: orange; */
	}
	.time{
		text-align: right;
		flex: 1;
		font-size: 0.25rem;
		/* line-height: 0.25rem; */
		margin-top: 0.11rem;
	}
    .msg-time{
    	color: #d0d0d0;
    	float: right;
    	font-size: 0.28rem;
    	margin-right: 5%;
    }

    .msg-name{
    	float: left;
    	font-size: 0.28rem;
    	margin-left: 4px;
    }

	.msg{
		font-size: 0.28rem;
	    margin-left: 10%;
        width: 80%;
	}
	
	.msgNull{
		font-size: 0.32rem;
    	padding-left: 2.5rem;
		position: fixed;
	    background-color: #fff;
	    width: 100%;
	    height: 100%;
	    top: .88rem;
	    padding-top: .5rem;
	}
	.main{
		word-wrap: break-word;
		margin-top: 1rem;
		width: 100%;
		margin-bottom: -0.7rem;
		border-bottom: 1px solid #d0d0d0;
	}
	.receiveImg{
		float: left;
		width: .5rem;
		margin-left: 5%;
		margin-top: -4px;
	}

</style>




