<template>
	<div>
	    <header>
	        <router-link to='/' class="goback"></router-link>
			<p>已发消息</p>
		</header>
		<div class="main" v-if="msgs.length==0">
		<span class="msgNull"><Icon style="color: #39f" type="information-circled"></Icon>&nbsp;&nbsp;&nbsp;暂无已发消息</span>
		</div>
		<div v-else>
			<div class="sentCont">
		<mt-loadmore :bottomDistance=70 :bottomLoadingText="null" :bottomDropText="null" :bottomPullText="null" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" >
				<div class="sentMain">
					<!-- 信息内容 -->
					<div class="sentList" v-for="(item,v) in msgs">
						<div class="sentListName">
							<div>
								<img src="../../gzxtkt/images/login-icon06.png" alt="图片"></div>
								<div style="margin-left:0.15rem">
									<span style="font-size:0.28rem;color: #40cc8b">{{item.msgtypes}}&nbsp;<router-link :to="'/yfxxDetails?demo='+item.pcid">查看详情({{pccounts[v]}})</router-link>&nbsp;&nbsp;&nbsp;<img v-if="item.ds==1 && item.dsflag==1" src="../../gzxtkt/images/time.png" @click="handleClick(v)">
									</span>
						        </div>
								<div style="color:#ccc" class="time">{{item.sendtime}}</div>

							</div>
							<div class="sentListCont">
								<p style="line-height:0.4rem;">{{item.msg}}</p>
							</div>
						</div>
					</div>
		         </mt-loadmore>
				</div>	
			</div>
		</div>
	</div>
</template>

<script> 
	import { MessageBox } from 'mint-ui';
	import kwz from '../utils/kwz.js'
	export default {
		data(){
			return {
				msgs: [],
				page: 1,
				countall: 0,
				pagesize: 10,
				pccounts:[],
				popupVisible:false,
				details:[],
				allLoaded:false,
			}
		},
		created: function(){
			if(!kwz.getOutboxList){
				var that=this;
				var url = kwz.project + '/getOutboxList'
				kwz.acall2({
					url : url,
					params : null,
					type : 'GET',
					success : function(data){
						if(data.StatusCode==200){
							that.msgs=data.data.T1;
							that.countall=data.data.T0[0].countall;
							kwz.getOutboxList=data.data;
							for (var i = 0; i < data.data.T1.length; i++) {
								that.pccounts.push(data.data.T1[i].pccount)
							}
						}else{
							alert("网络异常，稍后重试！");
						}
					},
				 })
			}else{
				this.msgs=kwz.getOutboxList.T1;
				this.countall=kwz.getOutboxList.T0[0].countall;
				for (var i = 0; i < kwz.getOutboxList.T1.length; i++) {
					this.pccounts.push(kwz.getOutboxList.T1[i].pccount)
				}
			}
		},
	methods: {
		loadBottom(){//上拉分页
			if(this.page>this.countall/this.pagesize){
				 this.allLoaded = true;// 若数据已全部获取完毕
  				 this.$refs.loadmore.onBottomLoaded();
			}	
		   	this.page++;
		   	var that=this;
		   	setTimeout(() => {
			var url= kwz.project +'/getOutboxList?page='+this.page;
			kwz.acall2({
				url : url,
				params : null,
				type : 'GET',
				success: function(data){
					if(data.StatusCode==200){
						that.msgs=that.msgs.concat(data.data.T1);
					}else{
						alert("网络异常，稍后重试！");
					}
				}
			}) }, 2000);
            // this.$refs.loadmore.onTopLoaded();
           },
         handleClick(v) {
       	 		var that=this;
       	 		MessageBox.confirm('确定取消定时发送?').then(action => {
				var url = kwz.project + '/setHddxCancle?pcid='+this.msgs[v].pcid;
				kwz.acall2({
					url : url,
					params : null,
					type : 'GET',
					success : function(data){
						if(data.StatusCode==200){
							alert(data.data.errinfo)
							history.go(0);
						}else{
							alert("网络异常，稍后重试！");
						}
					},
			 	})
			},regect=>{
			});
          		
       	    },
         }
	}
</script>

<style scoped>

	.sentCont{
		width: 100%;
		height: auto;
		background-color: white;
		padding: 0.2rem 0.3rem;
		box-sizing: border-box;
		position: relative;
    	top: 0.88rem;
	}
	.sentMain{
		width: 100%;
		height: auto;
	}
	.sentList{
		width: 100%;
		height: auto;
		border-bottom: 1px solid #e3e3e3;
		margin-bottom: 0.15rem;
	}
	.sentListName{
		width: 100%;
		height: auto;
		/* background-color: #b4c029; */
		display: flex; 
	}
	.sentListCont{
		width: 100%;
		height: auto;
		padding: 0.2rem;
		box-sizing: border-box;
		font-size: 0.31rem;
		/* background: orange; */
	}
	.time{
		text-align: right;
		flex: 1;
		font-size: 0.25rem;
		/* line-height: 0.25rem; */
		margin-top: 0.11rem;
	}
    .msg-time{
    	color: #d0d0d0;
    	float: right;
    	font-size: 0.28rem;
    	margin-right: 5%;
    }

    .msg-name{
    	float: left;
    	font-size: 0.28rem;
    	margin-left: 4px;
    }

	.msg{
		font-size: 0.28rem;
	    margin-left: 10%;
        width: 80%;
	}
	
	.msgNull{
		font-size: 0.32rem;
    	padding-left: 2.5rem;
		position: fixed;
	    background-color: #fff;
	    width: 100%;
	    height: 100%;
	    top: .88rem;
	    padding-top: .5rem;
	}
	.main{
		word-wrap: break-word;
		margin-top: 1rem;
		width: 100%;
		margin-bottom: -0.7rem;
		border-bottom: 1px solid #d0d0d0;
	}
	/*.receiveImg{
		float: left;
		width: .5rem;
		margin-left: 5%;
		argin-top: -4px;
	}*/
	div img{
		width:0.4rem;
		height:0.4rem;
		line-height:0.6rem;
	}
	span img{
		width: 0.4rem;
    	height: 0.4rem;
    	line-height: 0.6rem;
   		margin-bottom: -7px;
	}


</style>




