<template>
	<div>
		<div class="footer">
			<router-link :to="{name: s.href}" v-for="(s, i) in src" :key='s.href'>
				<span :class='bg'></span >
				<p :class='{checked: i==show}'>{{s.title}}</p>
			</router-link>
		</div>
	</div>

</template>
<style scoped>
	.footer{
		width: 100%;
		height: 1.11rem;
		border-top: 0.01rem solid #e7e7e7;
		position: fixed;
		bottom: 0px;
		left: 0px;
		z-index: 99999;
		display: flex;
		display: -webkit-flex;
		flex-flow: row;
		background-color: #fff;
	}
	.footer a{
		display: block;
		flex: 1;
		position: relative;
	}
	.footer a span{
		display: block;
		margin: 0 auto;
		/* margin-top: 0.15rem; */
		width: 0.46rem;
		height: 0.46rem;
		background-size: 100% 100%;
	}
	.footer p{
		font-size: 0.25rem;
		color: #b3b3b3;
		text-align: center;
		margin-top: 0.1rem;
	}
	.footer p.checked{
		color: #0caedf;
	}
	.footer a:nth-child(1) span{
		background-image: url('../../gzxtkt/images/main_icon01.png');
	}
	.footer a:nth-child(2) span{
		background-image: url('../../gzxtkt/images/main-icon02.gif');
	}
	/*.footer a:nth-child(3) span{
		background-image: url('../../gzxtkt/images/u2.png');
	}*/
	.footer a:nth-child(3) span{
		background-image: url('../../gzxtkt/images/main-icon03.png');
	}
	.footer a:nth-child(1) span.index{
		background-image: url('../../gzxtkt/images/main_icon01_pre.gif');
	}
	.footer a:nth-child(2) span.zx{
		background-image: url('../../gzxtkt/images/main_icon02_pre.gif');
	}
	/* .footer a:nth-child(3) span.lt{
		background-image: url('../../gzxtkt/images/pic_bjq.png');
	} */
	.footer a:nth-child(3) span.main{
		background-image: url('../../gzxtkt/images/main_icon03_pre.png');
	}
</style>
<script>
	export default {
		data(){
			return {
				src: [
					{href: 'index', value: 'index', title: '沟通'},
					{href: 'zx', value: 'zx', title: '学习'},
					{href: 'main', value: 'main', title: '我的'}
				],
				flag: 0
			}
		},
		computed: {
			bg: function(){
				if(this.show==0){
					return 'index'
				}
				if(this.show==1){
					return 'zx'
				}
				if(this.show==2){
					return 'main'
				}
			}
		},
		props: ['show']
	}
</script>


