<template>
	<div>
		<header v-if="h_flag">
	       <router-link to='/qinqing' class="goback"></router-link>
			<p>选择发送对象</p>
			<div class="find_icon" @click="h_flag=false"><Icon type="search"></Icon></div>
		</header>
		<div v-if="!h_flag" class="find_msg">
				<div class="header_icon">&nbsp;<Icon type="search"></Icon></div>
				<div><input v-model="findContent" type="text" name="" placeholder=" 搜索用户名/手机号码"></div>
				<button class="find_qx" @click="find_quxiao">取消</button>
		</div>
		<div class="main">
			<div>
				<div class="stu_list">
					<div v-for="(item,v) in concatsList">
						<div class="line_tea"></div>
						<div class="tea" @click="getId(item,v)">
						    <div class="circle" v-if="!item.isChecked" :id='"ids"+v'><Icon type="ios-circle-outline"></Icon></div>
						    <div class="checkmark" v-else :id='"ids"+v'><Icon type="ios-checkmark"></Icon></div>
							<div class="mobile">
								<div>{{item.realname}}</div>
								<div>({{item.authmobile}})</div>
							</div>
						</div>
					</div>
					<div class="line_tea"></div>
					<div class="ssjg" v-if="flag_show">
						<div class="icon_show"><Icon type="information-circled"></Icon></div>
						<div>暂无相关搜索结果</div>
					</div>
					<router-link :to="'/qinqing?ids='+ids">
						<div class="submit">确认</div>
					</router-link>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import kwz from '../utils/kwz.js'
	export default {
		data() {
			return {
				isStu:false,
				isTea:true,
				flag:true,
                model1: '',
                model2: '',
                h_flag:true,
                findContent:'',
				concatsList:[],
				teachers:[],
				gradeList:[],
				classList:[],
				find_flag:false,
				val1:'',
				val2:'',
				ids:[],
				flag_show:false,
			}
		},
		 watch: {
		 	findContent:function(val,oldval){
		 		var that=this;
		 		if(/^\d+$/.test(val)){//判断是否为数字
		 			var mobile=val;
		 			var uname='';
		 		}else{
		 			var mobile='';
		 			var uname=encodeURI(val);
		 		}
		        var url=kwz.project+'/getConcatsList?uname='+uname+'&mobile='+mobile;
		        kwz.acall2({
		            url:url,
		            params:null,
		            type:'GET',
		            success:function(res){
		                if (res.StatusCode==200) {
		                    that.concatsList=res.data;
		                    if(that.concatsList==''){
		                    	that.flag_show=true;
		                    }else{
		                    	that.flag_show=false;
		                    }
		                }  
		            }
		        });
		 	},
		 	
		 },
		created:function() {
			var that=this;
	            var url=kwz.project+'/getConcatsList';
	            kwz.acall2({
	                url:url,
	                params:null,
	                type:'GET',
	                success:function(res){
	                    if (res.StatusCode==200) {
	                         that.concatsList=res.data;
	                    }  
	                }
	            });
			
		},
		methods:{
			getId(item,i){
				if(item.isChecked!=undefined && item.isChecked){
					this.$set(this.concatsList[i],'isChecked',false)
				}else{
					this.$set(this.concatsList[i],'isChecked',true)
				}	
				if(this.ids.indexOf(item.uid)<0){
					this.ids.push(item.uid);
				}else{
					for(var i=0; i<this.ids.length; i++) {
    					if(this.ids[i] == item.uid) {
      					   this.ids.splice(i, 1);
     					   break;
    					}
  					}
				}
			},
			find_quxiao(){
				this.flag_show=false;
				this.h_flag=true;
				var that=this;
				var url_tea=kwz.project+'/getConcatsList';
	            kwz.acall2({
	                url:url_tea,
	                params:null,
	                type:'GET',
	                success:function(res){
	                    if (res.StatusCode==200) {
	                         that.concatsList=res.data;
	                    }  
	                }
	            });
				
			},
		}
	}
</script>
<style scoped>
	.main{
		margin-top: .88rem;
		position: relative;
		overflow-x: hidden;
		width: 100%;
		height: 100%;
		background-color: #fff;
	}
	.tea{
		display: flex;
	}
	.mobile{
		overflow: auto;
		width: 100%;
	}
	.mobile div:nth-child(1){
		font-size: .32rem;
		margin-bottom: .3rem;
    	margin-top: .3rem;
    	float: left;
    	margin-left: 6%;
	}
	.mobile div:nth-child(2){
		font-size: .32rem;
		margin-bottom: .3rem;
    	margin-top: .3rem;
    	float: right;
    	margin-right: 6%;
	}
	.uname{
		font-size: .32rem;
		margin-bottom: .3rem;
    	margin-top: .3rem;
    	float: left;
    	margin-left: 5%;
	}
	.line{
		border-top: 1px solid #e7e7e7;
		position: fixed;
	}
	.line_tea{
		border-top: 1px solid #e7e7e7;
		width: 90%;
		margin-left: 5%;
	}
	/*.stu_list{
		position: relative;
		margin-top: .8rem;
	}*/
	.find_stu{
	    position: fixed;
	    width: 100%;
	    color: #fff;
	    display: -webkit-box;
	    display: -ms-flexbox;
	    display: flex;
	    z-index: 9;
	}
	.find_icon{
		position: absolute;
	    margin-top: -.8rem;
	    font-size: .5rem;
	    color: #fff;
	    margin-left: 7rem;
	}
	.find_msg{
	    display: flex;
	    margin-top: -.9rem;
	    position: fixed;
	    z-index: 9;
	    background-color: #fff;
	    width: 100%;
	    height: .9rem;
	}
	.find_msg div input{
		font-size: .32rem;
	    width: 6rem;
	    height: .7rem;
	    margin-top: .1rem;;
	    background-color: #e7e7e7;
	    border: none;
	    border-radius: 0;
	    border-bottom: 1px solid #ccc;
    	border-top: 1px solid #ccc;
	}
	.header_icon{
		border-radius: 0;
		font-size: .4rem;
	    background-color: #e7e7e7;
	    height: .7rem;
	    margin-top: .1rem;
	    padding-top: .05rem;
	    margin-left: .3rem;
	    border-bottom-left-radius: 5px;
    	border-top-left-radius: 5px;
    	border-bottom: 1px solid #ccc;
    	border-top: 1px solid #ccc;
    	border-left: 1px solid #ccc;
	}
	.find_qx{
		font-size: .3rem;
	    color: #0caedf;
	    background-color: #fff;
	    height: .7rem;
	    margin-top: .1rem;
	    padding-top: .1rem;
	    border: 1px solid #ccc;
	}
	.circle{
		font-size: .5rem;
		margin-top: .2rem;
	    margin-left: .;
	    margin-left: .4rem;
	}
	.checkmark{
		font-size: .5rem;
	    color: #0cacde;
	    margin-top: .2rem;
	    margin-left: .;
	    margin-left: .4rem;
	}
	.submit{
		text-align: center;
	    height: 1rem;
	    background-color: #0cacde;
	    color: #fff;
	    font-size: .35rem;
	    margin: 5%;
	    padding-top: .25rem;
	    border-radius: 25px;
	}
	.ssjg{
		display: flex;
		color: red;
	    font-size: .3rem;
	    /* padding-top: .2rem; */
	    margin-top: .2rem;
	    margin-bottom: -.5rem;
	    margin-left: 33%;
	}
	.icon_show{
		font-size: .4rem;
		margin-top: -.08rem;
	}

</style>


