<template>
	<div>
		<!-- 广告轮播图 -->
		<ad></ad>
		<div class="content">
			<!-- <div @click="btnRight" class="cont_fx fx_l"><img src="../../gzxtkt/images/xs_pic_more02.png" class="fx_img"/></div> -->
			<!-- <div @click="btnLeft" class="cont_fx fx_r"><img src="../../gzxtkt/images/xs_pic_left.png"  class="fx_img" /></div> -->
			<!-- 老师端栏目 -->
			<div class="panel" v-if="utype=='1'">
				<div v-for="r in t_list" :key='r.key' class="panel_li" >
					<router-link :to="{path: r.href}" style="width: 2.2rem">
						<img :src="r.src" class="icon_0"></img>
						<!-- <span v-show="r.flag" class="icon_1"></span> -->
						<p>{{r.title}}</p>
                    </router-link>
				</div>
			</div>
			<!-- 家长端栏目 -->
			<div class="panel" v-if="utype=='3'" >
				<div v-for="(r,v) in p_list" :key='r.key' class="panel_li" >
					<router-link :to="{path: r.href}"  style="width: 2.2rem">
						<img :src="r.src" class="icon_0"></img>
						<!-- <span v-show="r.flag" class="icon_1"></span> -->
						<p>{{r.title}}</p>
                    </router-link>
				</div>
			</div>
			<div class="line"></div>
		</div>
		<div style=" margin-bottom: 1.9rem; ">
			<div class="jyzx">
				<span class="jyzx_title"><span class="title_img" >&nbsp;</span>最新消息</span>
			</div>
			<div class="cont">
			
			<div class="tx" @click="tan(v,msgs[v].msgid)"  v-for="(item,v) in msgs">
				<i v-if="item.flag==0" class="dian"></i>
				<div class="tx_L">
					<img v-if="item.senduphoto ==''" src="../../gzxtkt/images/alal.png" alt="" style="width:98%;height:100%;">
					<img v-else :src="imgurl+item.senduphoto" alt="" style="width:98%;height:100%;">
				</div>
				<div class="tx_R clear">
					<span style="margin-bottom: 0.15rem;font-size:0.3rem;float:left;"> {{item.sendname}}</span>
					<span style="font-size:0.25rem;color:#ccc;text-align:right;float:right">{{item.recvtime}}</span>					
					<p style="width: 5.0rem;font-size:0.25rem;display:inline-block;overflow: hidden; text-overflow:ellipsis; white-space: nowrap;color:#999">{{item.msg}}
					</p>
				</div>
			</div>

			
		</div>
		</div>
		<div id="tan">
			<div style="background-color:white;border-radius:0.2rem;padding:0.2rem;" >
				<p class="xq">发送人：<span style="font-weight:400;margin-left:0.1rem">{{tempName}}</span></p>
				<p class="xq">发送时间：<span style="font-weight:400;margin-left:0.1rem;color:#ccc">{{tempTime}}</span></p>
				<div id="show">
					<p style="font-size:0.3rem;margin:0.1rem 0;">
						<span style="font-weight: bold;padding:0.1rem;">消息内容：</span>{{tempMsg}}						
					</p>				
				</div>
				<div @click="del" style="text-align:center;">
					<!-- <mt-button size="small" type="danger">确定</mt-button> -->
					<Button style="background-color: #0caedf" type="success" long>确定</Button>
				</div>

			</div>	
		</div>
		<div id="zhe"></div>
		<foot :show="0"></foot>
	</div>
</template>

<script>
	import f from './footer.vue'
	import ad from './ad.vue'
	import kwz from '../utils/kwz.js'
	export default {
		data(){
			return {
				imgurl:'',
				utype:1,
				msgs:[],
				tempMsg:'',
				tempTime:'',
				contentId:null,
				tempName:'',
				some:null,
				// 功能模块列表
				stdList:[],
				t_list: [
					{href: '/fbrw', title: '发送信息', src: '../../gzxtkt/images/icon01.png', flag: true},
					{href: '/ysxx',title: '已收信息', src: '../../gzxtkt/images/icon02.png', flag: false},
					{href: 'yfxx',title: '已发信息', src: '../../gzxtkt/images/icon03.png', flag: false},
					{href: 'sendOA',title: '发送OA', src: '../../gzxtkt/images/icon04.png', flag: false},
					{href: 'ystz',title: '已收OA', src: '../../gzxtkt/images/icon05.png', flag: false},
					{href: 'yftz',title: '已发OA', src: '../../gzxtkt/images/icon06.png', flag: false},
					// {href: 'tzzj',title: '联系客服', src: '../../gzxtkt/images/icon07.png', flag: false},
					{href: 'classList',title: '班级空间', src: '../../gzxtkt/images/icon07.png', flag: false},
					{href: 'kaoqin',title: '查看考勤', src: '../../gzxtkt/images/icon08.png', flag: false},
					{href: 'txl',title: '通讯录', src: '../../gzxtkt/images/icon09.png', flag: false},
					{href: 'qinqing',title: '亲情短信', src: '../../gzxtkt/images/qinqing.png', flag: false},

				],
				p_list: [
					{href: '/fbrw', title: '发送信息', src: '../../gzxtkt/images/icon01.png', flag: true},
					{href: '/ysxx',title: '已收信息', src: '../../gzxtkt/images/icon02.png', flag: false},
					{href: 'yfxx',title: '已发信息', src: '../../gzxtkt/images/icon03.png', flag: false},
					// {href: 'ystz',title: '已收通知', src: '../../gzxtkt/images/icon04.png', flag: false},
					{href: 'classList',title: '班级空间', src: '../../gzxtkt/images/icon07.png', flag: false},
					{href: 'kaoqin',title: '查看考勤', src: '../../gzxtkt/images/icon08.png', flag: false},
				],
			}
		},
		components: {
			'foot': f, // 添加底部导航组件
			'ad':ad
		},
		created: function(){//最新消息
			this.imgurl= (kwz.project+"/").replace("api/wx/","");
			let that=this;	
			var url_xx = kwz.project + '/getNewMsg?pagesize=5'
			kwz.acall2({
				url : url_xx,
				params : null,
				type : 'GET',
				success : function(data){
					if(data.StatusCode==200){
						that.msgs=data.data.T1;
					}else{
						alert("网络异常，稍后重试！");
					}
				},
			 });

			if(!kwz.getMyInfo){
				var url = kwz.project + '/getMyInfo'
				kwz.acall2({
					url : url,
					params : null,
					type : 'GET',
					success : function(data){
						if(data.StatusCode==200){
							that.utype=data.data.utype;
							// kwz.utype=that.utype;
							kwz.getMyInfo=data.data;
						}else{
							alert("网络异常，稍后重试！");
						}
					},
				 });
			}else{
				this.utype=kwz.getMyInfo.utype;
			}
		},
		
		methods:{
			// btnRight() {
			// 	if ($('.panel').offset().left<=-300) {
			// 		// $('.panel').animate({left: '0'}, 1000);
			// 	}else{
			// 		$('.panel').animate({left: '-=7rem'}, 100); 
			// 	}			
			// },
			// //点击向右移动
			// btnLeft () {
			// 	let w = $('.panel').offset().left;
			// 	if ($('.panel').offset().left >= 17 && $('.panel').offset().left <= 60) {
			// 		$('.panel').animate({left: '0'}, 100);
			// 	}else{
			// 		$('.panel').animate({left: '+=7rem'}, 100);
			// 	}
			// },
			//查看信息
			tan (v,id) {
				this.tempMsg=this.msgs[v].msg;
				this.tempTime=this.msgs[v].recvtime;
				this.tempName=this.msgs[v].sendname;
				$("#zhe").show();
				$('#tan').show(100)
				var url_xx = kwz.project + '/editMsgStatus?msgid='+id;
				kwz.acall2({//更新消息状态
					url : url_xx,
					params : null,
					type : 'GET',
					success : function(data){
						if(data.StatusCode==200){
							if(data.status=="fail"){
                          	alert(data.data.errinfo);
						}
						}else{
							alert("网络异常，稍后重试！");
						}
					},
			 	})
			},
			//删除弹窗msg[].flag
			del() {
				var that=this;
				$("#zhe").hide();
				$('#tan').hide(100)
				var url_xx = kwz.project +'/getNewMsg?pagesize=5'
				kwz.acall2({
					url : url_xx,
					params : null,
					type : 'GET',
					success : function(data){
						if(data.StatusCode==200){
							that.msgs=data.data.T1;
						}else{
							alert("网络异常，稍后重试！");
						}
					},
				 })
			}
		}
	}
</script>

<style scoped>
	.dian{
		 display: inline-block;
   	 	width: 0.17rem;
    	height: 0.17rem;
    	background-color: red;
    	border-radius: 100%;
    	position:absolute;left:1.4rem

	}
	#show{
		width: 100%;
		min-height: 3rem;
	}
	#tan{
		width: 100%;
		height: auto;
		position: fixed;
		top: 2rem;
		z-index: 9999999999999999!important;
		/* border- */
		display: none;
		padding: 0.2rem 0.3rem;
		box-sizing: border-box;    
		/* background-color: white; */
	}
	#zhe {
		width: 100%;
		height: 100%;
		background-color: #4a4b4d;
		position: fixed;
		left: 0px;
		top: 0px;
		opacity: 0.5;
		z-index: 999999;
		display: none;
	}
	.tx{
		width: 100%;
		height: 1.6rem;
		padding: 0.2rem 0.3rem;
		box-sizing: border-box;
		display: flex;
		background: #fff;
		border-bottom: 1px solid #e7e7e7;
	}
	.tx_L{
		flex: 0.2;
		height: 1.2rem;
		width: 100%;

	}
	.tx_R{
		flex: 0.9;
		height: 1.2rem;
		width: 100%;
		padding: 0.1rem 0.2rem;
		box-sizing: border-box;
	}
	.tx :active{
		opacity: 0.5;
	}
	.dc{
		position: absolute;
		top: 0px;
		bottom: 0px;
		width: 100%;
		background-color: #fff;
		z-index: 0;

	}
	.title_img{
		background-color: #70a1e8; 
		margin-right: 0.1rem;}
	.jyzx{
		width: 100%; 
		height: 0.88rem; 
		background-color: #fff;
		margin-top: 0.2rem;
		border-bottom: 0.01rem solid #e7e7e7;
		padding: 0 0.2rem ; 
		font-size: 0.36rem;
		display: flex;
		align-items: center; 
		justify-content: space-between;
	}
	.jyzx_title{
		color: #70a1e8;  
	}
	.yjzc_more{
		font-size: 0.28rem; 
		color: #838383;  
	}
	.yjzc_more_img{
 		width: 0.16rem; 
 		height: 0.26rem; 
 		margin-left: 0.1rem;
	}

	.jyzx_list{
		width: 100%;  
		height: 1.6rem; 
		background-color: #fff; 
		border-bottom: #e7e7e7 0.01rem solid;
		overflow: auto;
		flex-direction: row;
		align-items: center; 
		justify-content: space-between;
	}
	.jyzx_l{
		width: 1.8rem; 
		height:1.18rem; 
		border-radius: 0.03rem;  
		text-align: center; 
		vertical-align: middle;
		margin:0.2rem; 
	}
	.jyzx_r{
		height: 100%; 
		width: 4.8rem; 
		margin: 0rem 0.2rem; 
		float: left; 
	}
	.record {
		display: flex;
		align-items: center; 
		justify-content: space-between;
		width: 100%; 
		height: 0.6rem;
		font-size: 0.28rem; 
		color: #828282; 
	}
	.jyzx_titil{
		color:#333; 
		font-size:0.34rem; 
		line-height: 0.7rem; 
		margin-top: 0.2rem;
		overflow:hidden; 
		text-overflow:ellipsis; 
		white-space:nowrap;
	}
	.content{
		margin-bottom: 0.2rem;
		width: 100%;
		height: 100%; 
		background-color: #fff;
		overflow: auto;	
	}
	.cont_fx{
		width: 5%; 
		background-color: #fff; 
		position: absolute;  
		z-index: 999;height: 2rem;  
		vertical-align: middle;
	}  
	.fx_l{ 
		right: 0;
		text-align: left;
	}
	.fx_r{ 
		left: 0;
		text-align: right;
	}
	.fx_img{ 
		width: 0.20rem; 
		height: 0.36rem;
		margin-top: 0.85rem;
	}
	.panel{
		overflow-y:hidden;
		display: flex;
		display: -webkit-flex;
        flex-flow:row wrap;
		/*margin: 0 5%;*/
		position: relative;
	}
	.panel_li{
		position: relative;
		padding: 0.1rem 0.13rem;
		width: 1.6rem;
		display: flex; 
		margin-left: .2rem;
    	margin-bottom: -.2rem;
	}
	.icon_1{
		position: absolute; 
		top: 0.25rem;
		right: 0.4rem;
		display: inline-block;
		width: 0.17rem;
		height: 0.17rem;
		background-color: #ff6666;
		border-radius: 100%;
	}
	.icon_0{
		margin: 0 auto;
		display: block;
		width: 1rem;
		height: 1rem;
		margin-bottom: -.2rem;
	}
	.panel_li p{
		margin-top: 0.22rem;
		font-size: 0.28rem;
		text-align: center;
		color: #838383;
	}

	.cont{
		position: relative;
		z-index: 3;
		width: 100%;
		height: auto;
		box-sizing: border-box;
	}
	.contList{
		width: 100%;
		height: 1.5rem;
		border-bottom: 1px solid #e7e7e7;
		display: flex;
	}
	.titl_H{
		flex: 5;
		width: 100%;
		height: 1.5rem;
	}
	.titl_H h3{
		margin-top: 0.3rem;
	}
	.Img_R{
		flex: 2.2;
		width: 100%;
		height: 1.5rem;
		padding: 0.15rem 0.1rem;
		box-sizing: border-box;

	}
	.xq{
		font-weight:900;
		border-bottom:1px dashed #ccc;
		padding:0.1rem;
		box-sizing:border-box;
		font-size:0.3rem
	}
	.line{
		border: 2px solid #e7e7e7;
	    margin-top: .2rem;
	}

</style>


