<template>
    <div class="sczp">
        <header>
           <img @click="back" class="fan" src="../../gzxtkt/images/xs_pic_fh.png"/>
            <p>上传照片</p>
            <p class="sc" @click="upload_ok">上传</p>
        </header>
       
        <div class="main">
            <div class="demo-upload-list" v-for="(item,index) in uploadList" :key="index">
                <template v-if="item.status === 'finished'">
                    <img :src="item.urls">
                    
                </template>
                <template v-else>
                    <Progress v-if="item.showProgress" :percent="item.percentage" hide-info></Progress>
                </template>
            </div>
            <Upload
                ref="upload"
                :show-upload-list="false"
                :default-file-list="defaultList"
                :on-success="handleSuccess"
                :format="['jpg','jpeg','png','GIF']"
                :max-size="2048"
                :on-format-error="handleFormatError"
                :on-exceeded-size="handleMaxSize"
                :before-upload="handleBeforeUpload"
                multiple
                type="drag"
                action="http://trtxkt.qgjydd.com/jxt/api/uploadFile?type=image"
                style="display: inline-block;width:58px;">
                <div style="width: 58px;height:58px;line-height: 58px;">
                    <Icon type="camera" size="20"></Icon>
                </div>
            </Upload>
            
       </div>
       
    </div>
</template>
<script>
    import kwz from '../utils/kwz.js'
    export default {
        data () {
            return {
                defaultList: [],
                imgName: '',
                visible: false,
                uploadList: [],
                imgurl:'',
                url_list:[],
                files_name:[],
                aName:'',
                xcId:'',
                Index:'0',
                hide1:false,
                xcList:[],
                imgurl:'',
                aId:'',
            }
        },
        created:function() {
            this.xcId = this.$route.query.xcid;
        },
        methods: {
           
            back() {
                history.go(-1);
            },
           
            handleSuccess (res, file) {//获取接口返回的照片路径，跟名字
                this.imgurl= (kwz.project+"/").replace("api/wx/","");
                file.url = this.imgurl+res.data[0].url;//路径
                file.urls = this.imgurl+'z'+res.data[0].url;//路径
                file.name = res.data[0].fieldName;//照片描述
                this.url_list.push(res.data[0].url);
                this.files_name.push(file.name);
            },
            handleFormatError (file) {
                this.$Notice.warning({
                    title: '文件格式不正确',
                    desc: '文件 ' + file.name + ' 格式不正确，请上传 jpg 或 png 格式的图片。'
                });
            },
            handleMaxSize (file) {
                this.$Notice.warning({
                    title: '超出文件大小限制',
                    desc: '文件 ' + file.name + ' 太大，不能超过 2M。'
                });
            },
            handleBeforeUpload () {
                const check = this.uploadList.length < 20;
                if (!check) {
                    this.$Notice.warning({
                        title: '最多只能上传 20 张图片。'
                    });
                }
                return check;
            },
            upload_ok(){//添加照片到相册
                kwz.getPhotoList='';
                kwz.getAlbumList='';
                let xcId = this.xcId
                for (var i = 0; i < this.url_list.length; i++) {
                    var that=this;
                    var url = kwz.project + '/addPhoto?aid='+xcId+'&pintro='+this.files_name[i]+'&path='+this.url_list[i]; 
                    kwz.acall2({
                        url : url,
                        params : {"k":i},
                        type : 'GET',
                        success : function(data){
                            if(data.StatusCode==200){
                                if(data.params.k==that.url_list.length-1){
                                  alert(data.data.errinfo)   
                                }
                            }else{
                                alert("网络异常，稍后重试！");
                            }
                        },
                     });

               }
               history.go(-1);
            }
        },
        mounted () {
            // debugger
            this.uploadList = this.$refs.upload.fileList;
        }
    }
</script>
<style>
    #xzList{
        width: 100%;
        height: 100%;
        background-color: white;
        z-index: 99999999999999999999!important;
        position: relative;
        top: -1.76rem;
    }
    .fan{
        position: absolute;
        left: 0.25rem;
        top: 0.26rem;
        width: 0.37rem;
        height: 0.35rem;
    }
    .uploadzp{
        width: 100%;
        height: 1rem;
        position: relative;
        top:0.88rem;
        /* background-color: orange; */
        border-bottom: 1px solid #eee;
        padding: 0.2rem;
        box-sizing: border-box;
        line-height: 1.0rem;
        
        /* overflow: auto; */
    }
    .xzbj{
        flex:1;
        text-align:right;
        font-size:0.3rem;
    }
    .main{
        word-wrap: break-word;
        margin-top: 1rem;
        width: 100%;
        margin-bottom: -0.7rem;
        border-bottom: 1px solid #eee!important;
    }
    .demo-upload-list{
        /*margin-top: .88rem;*/
        display: inline-block;
        width: 60px;
        height: 60px;
        text-align: center;
        line-height: 60px;
        border: 1px solid transparent;
        border-radius: 4px;
        overflow: hidden;
        background: #fff;
        position: relative;
        box-shadow: 0 1px 1px rgba(0,0,0,.2);
        margin-right: 4px;
    }
    .demo-upload-list img{
        width: 100%;
        height: 100%;
    }
    .demo-upload-list-cover{
        display: none;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(0,0,0,.6);
    }
    .demo-upload-list:hover .demo-upload-list-cover{
        display: block;
    }
    .demo-upload-list-cover i{
        color: #fff;
        font-size: 20px;
        cursor: pointer;
        margin: 0 2px;
    }
    .sczp{
        position: fixed;
        background-color: #fff;
        height: 100%;
        width: 100%;
    }
    .sc{
        color: #fff;
        position: absolute;
        top: -2%;
        right: 3%;
        font-size: .40rem;
    }

    .xcList{
        width: 100%;
        height: 1.4rem;
        /* background-color: orange; */
        position: relative;
        /* top: 0.88rem; */
        padding: 0.2rem;
        box-sizing: border-box;
        border-bottom: 1px solid #eee;
        display: flex;
    }
    .Img{
        flex: 0.15;
        width: 100%;
        height: 100%;
        background-color: orange;
    }
    .msg{
        flex: 0.85;
        width: 100%;
        height: 100%;
        margin-left: 0.2rem;
        margin-top: 0.1rem;
        /* background-color: red; */
    }
    .msg p{
        font-size: 0.3rem;
    }
    .msg span{
        font-size: 0.20rem;
    }
    .msg span i{
        color: red;
    }
</style>



