<template>
	<div>
		<header>
	       <img @click="back" class="fan" src="../../gzxtkt/images/xs_pic_fh.png"/>
			<p>选择相册</p>
		</header>
		<div class="dc"></div>
		<!-- 相册列表 -->
		<div>
			<router-link :to="'/sczp?xcname='+xcarr">
				<div class="xcList" v-for="(item,v) in xcList" :key="v" @click="blxc(v)">
					<div class="Img">
						<img :src="imgurl+item.lastphoto" alt="" style="width:100%;height:100%;">
					</div>
					<div class="msg">
						<p>{{item.aname}}</p>
						<span><i>{{item.photonum}}</i> 张照片</span>
					</div>
				</div>
			</router-link>
		</div>
	</div>
</template>
<script>
	import kwz from '../utils/kwz.js'
	export default {
		data() {
			return {
				xzid:'',
				xcList:[],
				imgurl:'',
				xcarr:[],
			}
		},
		created:function() {
			this.xzid = this.$route.query.xzID;
			var url_xz = kwz.project + '/getAlbumList?claid='+this.xzid;
			this.imgurl= (kwz.project+"/").replace("api/wx/","");
			var that = this;
			kwz.acall2({
				url : url_xz,
				params : null,
				type : 'GET',
				success : function(data){
					if(data.StatusCode==200){
						that.xcList = data.data.T1;
					}else{
						alert("网络异常，稍后重试！");
					}
				},
			});
		},	
		methods:{
			//返回上一级
			back() {
				history.go(-1);
			},
			//拿到每个相册的id和名字
			blxc(v){
				this.xcarr.push(this.xcList[v].aid);
				this.xcarr.push(this.xcList[v].aname);

			}
		},
	}
</script>
<style scoped>
	.dc{
		position: absolute;
		top: 0px;
		bottom: 0px;
		width: 100%;
		background-color: #fff;
		z-index: 0;
	}
	.xcList{
		width: 100%;
		height: 1.4rem;
		/* background-color: orange; */
		position: relative;
		top: 0.88rem;
		padding: 0.2rem;
		box-sizing: border-box;
		border-bottom: 1px solid #eee;
		display: flex;
	}
	.Img{
		flex: 0.15;
		width: 100%;
		height: 100%;
		background-color: orange;
	}
	.msg{
		flex: 0.85;
		width: 100%;
		height: 100%;
		margin-left: 0.2rem;
		margin-top: 0.1rem;
		/* background-color: red; */
	}
	.msg p{
		font-size: 0.3rem;
	}
	.msg span{
		font-size: 0.20rem;
	}
	.msg span i{
		color: red;
	}
</style>


