<template>
	<div>
		<header>
			<router-link to='/' class="goback"></router-link>
			<p>新闻详情</p>
		</header>
		<div class="dc"></div>
		<!-- 新闻内容 -->
		<div class="mian">
			<div class="cont">
				<div style="border-bottom:1px solid #e5e5e5;">
					<h4 class="title" style="margin-bottom:0.2rem;font-size:0.30rem;font-weight: 900;">{{Newstitle}}</h4>
					<div style="display:flex;margin-bottom:0.15rem;">
						<div style="flex:1;text-align:right;font-size:0.25rem;color:#adadad">{{Newsrelease_date}}</div>
					</div>
				</div>
				<div style="width:100%;margin-top:0.3rem;margin-bottom:0.2rem">
					<p v-html="Newstxt" style="font-size:0.3rem;width:100%;word-wrap: break-word;line-height:0.42rem">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</p>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				Newstitle:'',
				Newsrelease_date:'',
				Newsorigin:'',
				Newstxt:'',
			}
		},
		created: function() {
			let contId = this.$route.query.demo
			let that=this;
			//根据不同的ID获取不同的新闻列表
			$.ajax({
				url: 'http://trtxkt.qgjydd.com/jxt/api/wx/getInfoDetail?contentid='+contId+'',
				dataType: 'json',
				type: 'GET',
				success: function(data){
						if(data.StatusCode==200){
							var ref=data.data[0];
							that.Newstitle = ref.title;
							that.Newsrelease_date = ref.release_date;
							that.Newstxt = ref.txt;
							}else{
								alert("网络异常，稍后重试！");
							}
						}
					})
		}
	}
</script>
<style scoped>
	.clear:after{
		content: '';
		display: block;
		clear: both;
	}
	.dc{
		position: absolute;
		top: 0px;
		bottom: 0px;
		width: 100%;
		background-color: #fff;
		z-index: 2;
	}
	.mian{
		width: 100%;
		height: auto;
		/* background-color: orange; */
		position: relative;
		z-index: 3;
		margin-top: 0.88rem;
		padding: 0.2rem;
		box-sizing: border-box;

	}
	.cont{
		width: 100%;
		height: auto;
		/* background-color: red; */
		/* border: 1px solid #333; */
	}
	.title{
		text-align: center;
		font-size: 0.4rem;
	}
</style>


